<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full d-flex">
                            <h5 class="modal-title">{{ $t('Assign') + ' / ' + $t('Transfer') }}</h5>
                            <button type="button" class="close" v-on:click="$emit('close')">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                            <span class="user-guide">
                                <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG"
                                    role="button" aria-expanded="false" aria-controls="collapseExample"> <em
                                        class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                                <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                    <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                        <div class="con" v-html="$t('UgAddTicketTransfer')"></div>
                                    </div>
                                </div>
                            </span>
                        </div>
                        <!-- <div class="modal-header">
                            <h5 class="modal-title">{{ $t('Assign') + ' / ' + $t('Transfer') }}</h5>
                            <button v-on:click="$emit('close')" href="javascript:;" class="close">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                            <span class="user-guide">
                                <a class="ancuserguide" data-toggle="collapse" href="#collapseReply" role="button"
                                    aria-expanded="false" aria-controls="collapseExample">{{ $t('UserGuide') }}</a>
                                <div id="collapseReply" class="divancuserguide collapse">
                                    <div id="scrollbarmergeuserguide" class="custom-scrollbar-js">
                                        <div class="con" v-html="$t('USER_GUIDE_TICKET_TRANSFER')"></div>
                                    </div>
                                </div>
                            </span>
                        </div> -->
                        <div class="modal-body">

                            <dynamic-form v-if="DataLoaded" lang="en" :buttons="buttons" :schema="FormSchema"
                                v-on:OnSubmit="onSubmit">
                                <template v-slot:tgslot-Description="{ data }">
                                    <ckeditor :editor="editor" @ready="onReady" v-model="data.fieldInfo.value">
                                    </ckeditor>
                                    <span name="Description" class="invalid-feedback d-block">{{ data.error }}</span>
                                    <!-- {{ data.fieldInfo }} -->
                                </template>
                                <!-- <template v-slot:tgslot="{ data }">
                                    <template v-if="data.fieldInfo.name=='Description'">
                                        <ckeditor :editor="editor" @ready="onReady" v-model="data.fieldInfo.value" ></ckeditor>
                                    </template>
                                    <template v-if="data.fieldInfo.name=='Content'">
                                        <div class="input-group" >
                                            <input type="text" class="form-control" :value="data.fieldInfo.name">
                                            <span class="input-group-append">
                                                <span class="input-group-text" id="spnOutOF">/Out of 7</span>
                                            </span>
                                        </div>
                                    </template>
                                </template> -->
                            </dynamic-form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../services/DataService';
//import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
//import CKEditor from '@ckeditor/ckeditor5-vue2';
export default {
    components: {
        //"ckAssignTransferEditor": CKEditor.component
    },
    props: {
        TicketId: {
            type: String,
            required: true
        },
        modulename: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
        callbackfunction: {
            type: Function
        }
    },
    data() {
        return {
            DataLoaded: 0,
            editor: this.CLASSIC_EDITOR,
            TransferTicketData: [],
            priorityList: [],
            DepartmentList: [],
            TicketType: null,
            Description: null,
            FileName: null,
            file: null,
            TicketId: 0,
            ticketModel: {
                assignedToId: '',
                replyTemplate: '',
                departmentId: '',
                transferDescription: '',
                priorityId: '',
                fileData: [],
                transferType: '',
                moduleName: '',
                subModuleCode: ''
            },
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Type'),
                            name: "Type",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeType
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Priority'),
                            name: "Priority",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Department'),
                            name: "Department",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeDropdown
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Assigned To'),
                            name: "AssignedTo",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Canned Reply'),
                            name: "CannedReply",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeTemplate
                            },
                            validationRules: "",
                        }

                    ]
                },
                {
                    group_name: "Attachment",
                    layoutType: "single",
                    Data: [
                        {
                            astype: "FileField",
                            showLabel: false,
                            name: "Attachment",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize: 5,
                                accept: [
                                    {
                                        fileType: 'png',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'jpg',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'pdf',
                                        iconClass: 'fa-file-archive-o'
                                    }
                                ],
                                onChange: this.GetBase64String,
                                showAddIcon: false,
                                onAddButtonClick: '',
                                multiple: true
                            },
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            settings: {
                                isInSlot: true
                            },
                            visibility: true,
                            validationRules: { "required": true, max: 200 },
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ]
        }
    },
    async created() {
        var vm = this;
        await vm.FetchData();
        await vm.BindTicketType();
        await vm.GetPrioritybyCompanyId();
        await vm.BindCannedReply();
        if (vm.TransferTicketData.isSupportTicket) {
            await vm.GetDepartmentListForSupport();
        }
        else {
            await vm.GetDepartmentListbyCompanyForClient();
        }
        vm.DataLoaded = 1;
    },
    methods: {
        FetchData: async function () {
            //debugger;
            var vm = this;
            var url = `ticketId=${vm.TicketId}`;
            await DataService.AssignTransferTicket(url).then(function (response) {
                vm.TransferTicketData = response.data.data;

            })
        },
        BindTicketType: async function () {
            var vm = this;
            if (vm.TransferTicketData.ticketTransferAssignEnabled == 1) {
                await (vm.FormSchema)[0].Data[0].config.options.push({
                    name: vm.$t('Assign'),
                    value: 'A'
                })
            }
            else {
                await (vm.FormSchema)[0].Data[0].config.options.push(
                    {
                        name: vm.$t('Assign'),
                        value: 'A'
                    },
                    {
                        name: vm.$t('Transfer'),
                        value: 'T'
                    })
            }

        },
        GetPrioritybyCompanyId: async function () {
            var vm = this;
            var url = `isSupportTicket=${vm.TransferTicketData.isSupportTicket}`;
            await DataService.GetPrioritybyCompanyId(url).then(async (response) => {
                if (response.data.length > 0) {
                    vm.priorityList = response.data;
                    vm.priorityList.forEach((item, index) => {
                        (vm.FormSchema)[0].Data[1].config.options.push({
                            value: item.prioritY_ID,
                            name: item.prioritY_NAME
                        })
                    })
                }
            });
        },
        GetDepartmentListForSupport: async function () {
            var vm = this;
            await DataService.GetDepartmentListForSupport().then(async (response) => {
                if (response.data.length > 0) {
                    vm.DepartmentList = response.data;
                    vm.DepartmentList.forEach((item, index) => {
                        (vm.FormSchema)[0].Data[2].config.options.push({
                            value: item.departmenT_ID,
                            name: item.departmenT_NAME
                        })
                    })
                }
            });
        },
        GetDepartmentListbyCompanyForClient: async function () {
            var vm = this;
            await DataService.GetDepartmentListbyCompanyForClient().then(async (response) => {
                if (response.data.length > 0) {
                    vm.DepartmentList = response.data;
                    vm.DepartmentList.forEach((item, index) => {
                        (vm.FormSchema)[0].Data[2].config.options.push({
                            value: item.departmenT_ID,
                            name: item.departmenT_NAME
                        })
                    })
                }
            });
        },
        BindCannedReply: async function () {
            var vm = this;
            await vm.TransferTicketData.replyTemplate.forEach((item, index) => {
                (vm.FormSchema)[0].Data[4].config.options.push({
                    value: item.value,
                    name: item.name
                })
            })
        },
        onChangeType: async function (event) {
            var vm = this;
            vm.TicketType = event.target.value;
        },
        onChangeDropdown: async function (event) {
            //debugger;
            var vm = this;
            var params = `departmentId=${event.target.value}&reqFrom=${vm.TicketType}`;
            // var params = `departmentId=${vm.TransferTicketData.departmentId}&reqFrom=${event.target.value}`;
            await DataService.GetUserListByDepId(params).then(async (response) => {
                (vm.FormSchema)[0].Data[3].config.options = response.data;
            })
        },
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },
        setFile: function (e) {
            var vm = this;
            vm.file = e.target.files[0];
            vm.FileName = e.target.files[0].name;
        },
        GetBase64String: function (evt) {
            try {
                // debugger;
                var vm = this;
                //alert(vm.apiurl);
                vm.fileData = [];

                var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                // Closure to capture the file information.
                reader.onload = (function (theFile) {
                    return function (e) {
                        var binaryData = e.target.result;
                        //Converting Binary Data to base 64
                        var base64StringEncoded = window.btoa(binaryData);
                        //showing file converted to base64

                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);

                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;
                        // debugger;
                        // var ggg = vm.configData;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            base64String: base64StringEncoded,
                            fileSize: f.size,
                            contentType: f.type
                        };

                        vm.fileData.push(fileObj);
                    };
                })(f);
                // Read in the image file as a data URL.
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
        },
        onChangeTemplate: async function (e) {
            var vm = this;
            vm.Description = e.target.value;
        },
        onSubmit: async function (formfields, val) {
            var vm = this;
            //debugger
            vm.ticketModel.TicketId = vm.TicketId;
            vm.ticketModel.transferType = vm._data.FormSchema[0].Data[0].value;
            vm.ticketModel.priorityId = vm._data.FormSchema[0].Data[1].value;
            vm.ticketModel.departmentId = vm._data.FormSchema[0].Data[2].value;
            vm.ticketModel.assignedToId = vm._data.FormSchema[0].Data[3].value;
            vm.ticketModel.replyTemplate = vm._data.FormSchema[0].Data[4].value;
            vm.ticketModel.transferDescription = vm._data.FormSchema[1].Data[1].value;
            vm.ticketModel.fileData = vm.fileData;
            vm.ticketModel.moduleName = vm.modulename;
            vm.ticketModel.subModuleCode = vm.submodulecode;
            var formData = JSON.stringify(vm.ticketModel);
            DataService.TransferOrAssignTicket(formData).then((response) => {
                try {
                    //debugger;
                    if (response.data != null && response.data != "" && response.data.result.MSG == "SUCCESS") {
                        if (vm.ticketModel.transferType == "A") {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("TICKET_ASSIGNED")]), "success", true, vm.$t("Alert"));
                            vm.Close();
                            vm.callbackfunction();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("TICKET_TRANSFER")]), "success", true, vm.$t("Alert"));
                            vm.Close();
                            vm.callbackfunction();
                        }
                    }
                    else {
                        vm.FileSrc = '';
                        $("input[type=text]").val('');
                    }
                }
                catch (exinner) {
                    return HandleErrorOccured(exinner, 'saveClaim');
                }
            });
        },
        Close: function () {
            var vm = this;
            vm._data.FormSchema[0].Data[0].value = null;
            vm._data.FormSchema[0].Data[1].value = null;
            vm._data.FormSchema[0].Data[2].value = null;
            vm._data.FormSchema[0].Data[3].value = null;
            vm._data.FormSchema[0].Data[4].value = null;
            vm._data.FormSchema[1].Data[1].value = null;
            vm.FileName = null;
            vm.file = null;
            vm.FormSchema[1].Data[0].config.onChange = this.GetBase64String;
            vm.$parent.isShowTransferTicket = false;
        }
    }
}
</script>