
<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <transition name="modal">
            <div class="modal d-block my-popups dialog">
                <div class="modal-dialog mw-1000">
                    <div class="modal-content">
                        <div class="modal-header">
                            <div name="headerForModal">
                                <h5 class="modal-title">{{ $t('Form') }}</h5>
                            </div>
                            <button type="button" title="" class="close" v-on:click="Close()" data-original-title="Close"><i
                                    class="fa fa-times" aria-hidden="true"></i></button>
                        </div>
                        <div class="modal-body setheight">
                            <template v-for="(Header, parentHeaderIndex) in GroupsOfFields">
                                <div class="col-lg-12 mb-3 px-0">
                                    <div class="heading-border mt-2" v-if="Header.header_name">
                                        <h5 class="h5"
                                            v-bind:class="{ 'text-left': Header.headerPositionType == 'left', 'text-center': Header.headerPositionType == 'center', 'text-right': Header.headerPositionType == 'right' }">
                                            <span>{{ Header.header_name }}</span>
                                        </h5>
                                    </div>
                                </div>
                                <!-- Section Control Start -->
                                <div class="row pl-3 pr-0" v-for="(group, parentIndex) in Header.SectionData">
                                    <div class="col-lg-12 mb-3">
                                        <div class="heading-border mt-2"
                                            v-if="GetLocalizedValue(group.group_name, group.group_display_name)">
                                            <h5 class="h5">
                                                <span>{{ GetLocalizedValue(group.group_name, group.group_display_name)
                                                }}</span>
                                            </h5>
                                        </div>
                                    </div>

                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0 d-flex flex-wrap">
                                        <div v-bind:class="{ 'col-sm-12 col-md-12 float-left': true, 'd-none': item.form_field_visibility == 'NO', 'col-lg-12 col-xl-12': item.layout_type == 'single', 'col-lg-6 col-xl-6': item.layout_type == 'double', 'col-lg-4 col-xl-4': item.layout_type == 'triple', 'col-lg-3 col-xl-3': item.layout_type == 'four' }"
                                            v-bind:id="item.name" v-for="(item, index) in group.FormFieldData"
                                            v-bind:key="index">
                                            <input type="hidden" v-model="item.value"
                                                v-if="item.form_field_visibility == 'NO'" v-bind:id="item.name" />
                                            <div class="form-group">
                                                <label>{{ GetLocalizedValue(item.label, item.display_name) }}:<span
                                                        v-bind:class="{ 'text-danger': item.is_required == true }"
                                                        v-if="item.is_required == true">*</span></label>
                                                <a href="javascript:;" v-if="!isBlank(item.user_guide)" v-bind:tabindex="-1"
                                                    data-toggle="popoveruserguide"
                                                    v-bind:data-content="GetLocalizedValue(item.user_guide)">
                                                    <i class="fa fa-question-circle-o text-popover"
                                                        style="font-size: 14px;"></i>
                                                </a>
                                                <div class="row">
                                                    <div class="col-12 float-left">

                                                        <!-- for img -->
                                                        <template v-if="item.dt_code == 'img'">
                                                            <Field :rules="GetValidation(item)" v-slot="{ field, errors }">
                                                                <div class="input-group">
                                                                    <input v-bind:class="{
                                                                        'form-control': true,
                                                                        'is-invalid': item.picklist_options != 'Lookup'
                                                                            && errors.length > 0
                                                                        , 'disabled': (item.name == 'commission_type' || item.name == 'agreement_type')
                                                                    }" v-validate="GetValidation(item)" type="file"
                                                                        id="file" ref="file"
                                                                        v-on:change="handleFileUpload($event, item)"
                                                                        v-bind:placeholder="GetFieldPlaceHolder(item.label, item.display_name)"
                                                                        v-bind:id="item.name" v-bind:name="item.name"
                                                                        v-bind:data-vv-as="GetLocalizedValue(item.name, item.display_name)" />
                                                                </div>
                                                                <span v-if="errors[0]" class="invalid-feedback d-block">{{
                                                                    errors[0]
                                                                }}</span>
                                                            </Field>
                                                        </template>


                                                        <div class="mt-2" id="divimagedisplayImageId"
                                                            v-if="item.dt_code == 'img'">
                                                            <!-- <div v-if="!item.value.bass64">
                                                                    <img class="img-fluid mb-2"
                                                                        src="~/Content/images/imagenotavailable.jpg"
                                                                        alt="Logo" style="max-height:99px;
                                                                 width:auto;" width="200" height="150" />
                                                                </div> -->
                                                            <div v-if="item.value.bass64">
                                                                <img :src="item.value.bass64" class="img-fluid mb-2"
                                                                    alt="Logo" style="max-height:99px; width:auto;"
                                                                    width="200" height="150" />
                                                            </div>
                                                        </div>

                                                        <!-- for date -->
                                                        <template v-if="item.dt_code == 'date'">
                                                            <Field :rules="GetValidation(item)" v-slot="{ field, errors }">
                                                                <DatePicker style="flex-wrap:nowrap;" :is24hr="is24hr"
                                                                    :mode="mode" :disabled="item.disabled" :masks="masks"
                                                                    :minute-increment="5" :class="{
                                                                        'input-group w-100': true, 'is-invalid':
                                                                            errors[0]
                                                                    }" v-model="item.value">
                                                                    <template
                                                                        v-slot="{ inputValue, inputEvents, togglePopover }">
                                                                        <input autocomplete="off" :disabled="item.disabled"
                                                                            v-bind:name="item.name" ref="inputDatePicker"
                                                                            v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }"
                                                                            :value="inputValue" v-on="inputEvents"
                                                                            :placeholder="item.label"
                                                                            @blur="onBlur($event, field)" />
                                                                        <div class="input-group-append">
                                                                            <span class="input-group-text"
                                                                                v-on="(!item.disabled) ? inputEvents : null">
                                                                                <i class="fa fa-calendar"></i>
                                                                            </span>
                                                                        </div>
                                                                    </template>
                                                                </DatePicker>
                                                                <span v-show="errors[0]" class="invalid-feedback d-block"
                                                                    ref="errrorMessage">{{ errors[0] }}</span>


                                                                <!-- <div class="input-group">
                                                                    <date-picker v-mod el="item.value"
                                                                        v-bind:name="item.name" v-bind:wrap="true"
                                                                        v-validate="GetValidation(item)"
                                                                        v-bind:value="formatDate(item.value)" v-bind:class="{
                                                                                'form-control': true, 'is-invalid': item.dt_code == 'date'
                                                                                    && errors.length > 0
                                                                            }"
                                                                        v-bind:config="(item.name == 'expected_close_date') ? DependentOption[0] : datePickerConfig"
                                                                        v-on:input="DependentDateObject(item, item.value)"
                                                                        v-bind:placeholder="GetFieldPlaceHolder(item.label, item.display_name)"
                                                                        v-bind:data-vv-as="GetLocalizedValue(item.name, item.display_name)"></date-picker>
                                                                    <div class="input-group-append">
                                                                        <span class="input-group-text datepickerbutton"
                                                                            :title="$t('SelectDate')">
                                                                            <i class="fa fa-calendar"></i>
                                                                        </span>
                                                                    </div>

                                                                </div>
                                                                <span v-if="errors[0]" class="invalid-feedback d-block">{{
                                                                    errors[0]
                                                                    }}</span> -->
                                                            </Field>
                                                        </template>

                                                        <!-- for text box -->
                                                        <template
                                                            v-if="item.picklist_options != 'Lookup' && item.dt_code != 'date' && item.dt_code != 'radio' && item.dt_code != 'select' && item.dt_code != 'textarea' && item.dt_code != 'checkbox' && item.dt_code != 'phone' && item.dt_code != 'img' && item.dt_code != 'signature'">
                                                            <Field :rules="GetValidation(item)" v-slot="{ field, errors }">
                                                                <input :readonly="isforshow" v-bind:class="{
                                                                    'form-control': true, 'is-invalid': item.picklist_options != 'Lookup' && errors.length > 0
                                                                    , 'disabled': (item.name == 'commission_type' || item.name == 'agreement_type')
                                                                }" v-validate="GetValidation(item)" type="text"
                                                                    v-bind:placeholder="GetFieldPlaceHolder(item.label, item.display_name)"
                                                                    v-model="item.value" v-bind:id="item.name"
                                                                    v-bind:name="item.name"
                                                                    v-bind:data-vv-as="GetLocalizedValue(item.name, item.display_name)" />
                                                                <span v-if="errors[0]" class="invalid-feedback d-block">{{
                                                                    errors[0]
                                                                }}</span>
                                                            </Field>
                                                        </template>

                                                        <!-- for text area -->
                                                        <template v-if="item.dt_code == 'textarea'">
                                                            <Field :rules="GetValidation(item)" v-slot="{ field, errors }">
                                                                <textarea v-bind:class="{
                                                                    'form-control': true, 'is-invalid': item.dt_code == 'textarea' && errors.length > 0
                                                                }" v-validate="GetValidation(item)" type="text"
                                                                    v-bind:placeholder="GetFieldPlaceHolder(item.label, item.display_name)"
                                                                    v-model.trim="item.value" v-bind:id="item.name"
                                                                    v-bind:name="item.name"
                                                                    v-bind:data-vv-as="GetLocalizedValue(item.name, item.display_name)"></textarea>
                                                                <span v-if="errors[0]" class="invalid-feedback d-block">{{
                                                                    errors[0]
                                                                }}</span>
                                                            </Field>
                                                        </template>


                                                        <!-- For Phone numbar -->
                                                        <template v-if="item.dt_code == 'phone'">
                                                            <Field :rules="GetValidation(item)" v-slot="{ field, errors }">
                                                                <div class="col-md-12 col-sm-12 col-lg-12 p-0">
                                                                    <vue-phone-number-input v-model="item.value"
                                                                        v-bind:name="item.name"
                                                                        :no-use-browser-locale="true"
                                                                        v-on:update="GetPhoneNumber(item, $event, 'spanPhone' + index)"></vue-phone-number-input>
                                                                    <span style="display:none"
                                                                        v-bind:id="'spanPhone' + index"
                                                                        class="invalid-feedback">Enter valid
                                                                        number.</span>
                                                                </div>
                                                            </Field>
                                                        </template>

                                                        <!-- for radio button toggle button start -->
                                                        <template
                                                            v-if="item.dt_code == 'radio' && MakeArray(item.picklist_options, item.dt_code).length == 2">
                                                            <Field :rules="GetValidation(item)" v-slot="{ field, errors }">
                                                                <div class="form-group">
                                                                    <label class="switch">
                                                                        <input
                                                                            class="form-check-input rdoticketfor custom-control-input dynamic"
                                                                            type="checkbox" v-bind:name="item.name"
                                                                            v-bind:data-vv-as="GetLocalizedValue(item.name, item.display_name)"
                                                                            v-bind:value="item.value"
                                                                            v-on:change="AssignToggleValues($event, item, MakeArray(item.picklist_options, item.dt_code), parentIndex, index)"
                                                                            v-bind:checked="makeCheckBoxSelected(MakeArray(item.picklist_options, item.dt_code), item.value, 'toggle')"
                                                                            v-bind:disabled="item.is_Disabled" tabindex="5">
                                                                        <span class="slider round">
                                                                            <span class="slider-yes">{{
                                                                                MakeArray(item.picklist_options,
                                                                                    item.dt_code)[0].name }}</span>
                                                                            <span class="slider-no">{{
                                                                                MakeArray(item.picklist_options,
                                                                                    item.dt_code)[1].name }}</span>
                                                                        </span>
                                                                    </label>
                                                                </div>
                                                                <span v-if="errors[0]" class="invalid-feedback d-block">{{
                                                                    errors[0]
                                                                }}</span>
                                                            </Field>
                                                        </template>

                                                        <!-- for radio button horizantal start -->
                                                        <template
                                                            v-if="item.dt_code == 'radio' && MakeArray(item.picklist_options, item.dt_code).length > 2 && item.optionsType">
                                                            <Field :rules="GetValidation(item)" v-slot="{ field, errors }">
                                                                <div class="form-control pl-0 border-0 bg-transparent">
                                                                    <div class="custom-control p-0">
                                                                        <div class="custom-control custom-radio custom-control-inline"
                                                                            v-for="option in MakeArray(item.picklist_options, item.dt_code)"
                                                                            v-bind:key="option.name">
                                                                            <input
                                                                                v-bind:id="'rdo_' + item.name + '_' + option.value"
                                                                                class="dynamic custom-control-input"
                                                                                v-model="item.value" v-bind:name="item.name"
                                                                                v-validate="GetValidation(item)"
                                                                                v-on:change="showDependentItem(item, option.value, parentIndex, index)"
                                                                                v-bind:data-resource="option.name"
                                                                                v-bind:value="option.value" type="radio"
                                                                                v-bind:checked="option.value == item.value"
                                                                                v-bind:data-vv-as="GetLocalizedValue(item.name, item.display_name)"
                                                                                v-bind:disabled="item.is_Disabled">
                                                                            <label
                                                                                class="custom-control-label universalradio-custom-control-label dynamic"
                                                                                v-bind:for="'rdo_' + item.name + '_' + option.value">{{
                                                                                    option.name }}</label>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                                <span v-if="errors[0]" class="invalid-feedback d-block">{{
                                                                    errors[0]
                                                                }}</span>
                                                            </Field>
                                                        </template>

                                                        <!-- for radio button vertical start -->
                                                        <template
                                                            v-if="item.dt_code == 'radio' && MakeArray(item.picklist_options, item.dt_code).length > 2 && !item.optionsType">
                                                            <Field :rules="GetValidation(item)" v-slot="{ field, errors }">
                                                                <div class="form-control pl-0 border-0 bg-transparent">
                                                                    <div class="custom-control p-0">
                                                                        <div class="custom-control custom-radio"
                                                                            v-for="option in MakeArray(item.picklist_options, item.dt_code)"
                                                                            v-bind:key="option.name">
                                                                            <input
                                                                                v-bind:id="'rdo_' + item.name + '_' + option.value"
                                                                                class="dynamic custom-control-input"
                                                                                v-model="item.value" v-bind:name="item.name"
                                                                                v-validate="GetValidation(item)"
                                                                                v-on:change="showDependentItem(item, option.value, parentIndex, index)"
                                                                                v-bind:data-resource="option.name"
                                                                                v-bind:value="option.value" type="radio"
                                                                                v-bind:checked="option.value == item.value"
                                                                                v-bind:data-vv-as="GetLocalizedValue(item.name, item.display_name)"
                                                                                v-bind:disabled="item.is_Disabled">
                                                                            <label
                                                                                class="custom-control-label universalradio-custom-control-label dynamic"
                                                                                v-bind:for="'rdo_' + item.name + '_' + option.value">{{
                                                                                    option.name }}</label>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                                <span v-if="errors[0]" class="invalid-feedback d-block">{{
                                                                    errors[0]
                                                                }}</span>
                                                            </Field>
                                                        </template>

                                                        <!-- for Checkbox horizantal start -->
                                                        <template
                                                            v-if="item.dt_code == 'checkbox' && item.ChkBoxoptionsType == 'HorizantalCheckBox'">
                                                            <Field :rules="GetValidation(item)" v-slot="{ field, errors }">
                                                                <div class="form-control pl-0 border-0 bg-transparent">
                                                                    <div class="form-check form-check-inline"
                                                                        v-for="option in MakeArray(item.picklist_options, item.dt_code)">
                                                                        <div class="custom-control custom-checkbox">
                                                                            <input
                                                                                v-bind:id="'chk_' + item.name + '_' + option.value"
                                                                                class="dynamic custom-control-input"
                                                                                v-bind:name="item.name"
                                                                                v-validate="GetValidation(item)"
                                                                                v-bind:data-resource-id="option.name"
                                                                                v-bind:value="option.value" type="checkbox"
                                                                                v-bind:checked="makeCheckBoxSelected(item.value, option.value)"
                                                                                v-on:change="onChangeCheckBox(item, option.value, $event)"
                                                                                v-bind:data-vv-as="GetLocalizedValue(item.name, item.display_name)">
                                                                            <label
                                                                                class="custom-control-label universal-custom-control-label pl-2 pr-1 dynamic"
                                                                                v-bind:for="'chk_' + item.name + '_' + option.value">{{
                                                                                    option.name }}</label>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                                <span v-if="errors[0]" class="invalid-feedback d-block">{{
                                                                    errors[0]
                                                                }}</span>
                                                            </Field>
                                                        </template>
                                                        <!-- for Checkbox horizantal end -->

                                                        <!-- for Checkbox Vertical start -->
                                                        <template
                                                            v-if="item.dt_code == 'checkbox' && item.ChkBoxoptionsType == 'VerticalCheckBox'">
                                                            <Field :rules="GetValidation(item)" v-slot="{ field, errors }">
                                                                <div class="form-control pl-0 border-0 bg-transparent">
                                                                    <div class="form-group">
                                                                        <div class='custom-control custom-checkbox'
                                                                            v-for="option in MakeArray(item.picklist_options, item.dt_code)">
                                                                            <input
                                                                                v-bind:id="'chk_' + item.custom_field_id + '_' + option.value"
                                                                                class="dynamic custom-control-input"
                                                                                v-bind:name="item.name"
                                                                                v-validate="GetValidation(item)"
                                                                                v-bind:data-resource-id="option.name"
                                                                                v-bind:value="option.value" type="checkbox"
                                                                                v-bind:checked="makeCheckBoxSelected(item.value, option.value)"
                                                                                v-on:change="onChangeCheckBox(item, option.value, $event)"
                                                                                v-bind:data-vv-as="GetLocalizedValue(item.name, item.display_name)">
                                                                            <label class='custom-control-label'
                                                                                v-bind:for="'chk_' + item.custom_field_id + '_' + option.value">
                                                                                {{ option.name }}
                                                                            </label>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                                <span v-if="errors[0]" class="invalid-feedback d-block">{{
                                                                    errors[0]
                                                                }}</span>
                                                            </Field>
                                                        </template>

                                                        <!-- for Checkbox Vertical end -->


                                                        <!-- custom drop down list -->
                                                        <template
                                                            v-if="item.picklist_options == 'Lookup' && item.dt_code == 'select' && item.dropdown_type == 'custom'">
                                                            <Field :rules="GetValidation(item)" v-slot="{ field, errors }">
                                                                <v-select label="name" v-bind:filterable="false"
                                                                    v-bind:name="item.name"
                                                                    v-validate="item.dependent_show_type == 1 ? GetValidation(item) : ''"
                                                                    v-bind:class="{
                                                                        'custom-vselect': vSelectValidate == true, 'is-invalid': item.picklist_options == 'Lookup' && item.dt_code == 'select' && item.dropdown_type == 'custom'
                                                                            && errors.length > 0
                                                                    }"
                                                                    v-bind:placeholder="GetFieldPlaceHolder(item.label, item.display_name, item.dt_code)"
                                                                    v-model="item.select_value" v-bind:value="item.value"
                                                                    v-bind:options="item.select_options"
                                                                    v-on:search="onSearch($event, item)"
                                                                    @@input="setSelectedValue(item, $event)"
                                                                    v-bind:checkvalue="item.value"
                                                                    v-bind:data-vv-as="GetLocalizedValue(item.name, item.display_name)">
                                                                    <template slot="no-options">
                                                                        {{ $t('TypeTosearch') }}
                                                                        {{ GetLocalizedValue(item.label,
                                                                            item.display_name) }}
                                                                    </template>
                                                                    <template slot="option" slot-scope="option">
                                                                        <div class="d-center">
                                                                            {{ option.name }}
                                                                        </div>
                                                                    </template>
                                                                    <template slot="selected-option" slot-scope="option">
                                                                        <div class="selected d-center">
                                                                            {{ option.name }}
                                                                        </div>
                                                                    </template>
                                                                </v-select>
                                                                <span v-if="errors[0]" class="invalid-feedback d-block">{{
                                                                    errors[0]
                                                                }}</span>
                                                            </Field>
                                                        </template>


                                                        <!-- normal drop down list -->
                                                        <template
                                                            v-if="item.picklist_options == 'Lookup' && item.dt_code == 'select' && item.dropdown_type == 'normal'">
                                                            <Field :rules="GetValidation(item)" v-slot="{ field, errors }">
                                                                <select v-bind:name="item.name" v-bind:class="{
                                                                    'form-control': true, 'is-invalid': item.picklist_options == 'Lookup' && item.dt_code == 'select' && item.dropdown_type == 'normal'
                                                                        && errors.length > 0
                                                                }" v-validate="GetValidation(item)"
                                                                    v-model="item.value"
                                                                    v-bind:data-vv-as="GetLocalizedValue(item.name, item.display_name)"
                                                                    v-on:change="FillDropdown(item, null); onchangechannel(item, group.LeadData)"
                                                                    mousedown.native="onKeyDown1">
                                                                    <option value="">{{ $t('Select') }}</option>
                                                                    <option
                                                                        v-for="option in MakeNormalArray(item.select_options)"
                                                                        v-bind:selected="item.value == option.value"
                                                                        v-bind:value="option.value">{{ option.name }}
                                                                    </option>
                                                                </select>
                                                                <span v-if="errors[0]" class="invalid-feedback d-block">{{
                                                                    errors[0]
                                                                }}</span>
                                                            </Field>
                                                        </template>


                                                        <template
                                                            v-if="item.picklist_options != 'Lookup' && item.dt_code == 'select'">
                                                            <Field :rules="GetValidation(item)" v-slot="{ field, errors }">
                                                                <select v-bind:name="item.name" v-bind:class="{
                                                                    'form-control': true, 'is-invalid': item.picklist_options != 'Lookup' && item.dt_code == 'select'
                                                                        && errors.length > 0
                                                                }" v-validate="GetValidation(item)"
                                                                    v-model="item.value"
                                                                    v-bind:data-vv-as="GetLocalizedValue(item.name, item.display_name)">
                                                                    <option value="">{{ $t('Select') }}</option>
                                                                    <option
                                                                        v-for="option in MakeArray(item.picklist_options, item.dt_code)"
                                                                        v-bind:selected="item.value == option.value"
                                                                        v-bind:value="option.value">{{ option.name }}
                                                                    </option>
                                                                </select>
                                                                <span v-if="errors[0]" class="invalid-feedback d-block">{{
                                                                    errors[0]
                                                                }}</span>
                                                            </Field>
                                                        </template>



                                                        <div class="ml-2 mt-1" v-if="UserType == 'CA'">
                                                            <a class="round-icon-small btn-dark theme-primary"
                                                                v-if="item.isPlusIcon == true">
                                                                <i class="fa fa-plus text-white pt-7 font-18"
                                                                    v-on:click="openaddIndustrypopup(item)"
                                                                    v-if="item.name == 'industry_type_id'"></i>
                                                                <i class="fa fa-plus text-white pt-7 font-18"
                                                                    v-on:click="openaddLeadSourcepopup(item)"
                                                                    v-if="item.name == 'channel_id'"></i>
                                                                <i class="fa fa-plus text-white pt-7 font-18"
                                                                    v-on:click="openaddRoleTitlepopup(item)"
                                                                    v-if="item.name == 'role_title_id'"></i>
                                                            </a>
                                                        </div>
                                                        <!-- <div v-if="errors.has(item.name)" class="invalid-feedback">{{
                                                                errors.first(item.name) }}</div> -->

                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <!-- Section Control End -->
                            </template>
                            <div class="row flex-row-reverse" v-if="isforshow == false">
                                <div class="col-lg-6 text-right">
                                    <a href="javascript:;" class="btn btn-success formbtn widthhalf mr-2"
                                        v-on:click="SubmitData()" :title="$t('Save')">
                                        <i class="fa fa-save pr-2"></i> {{ $t('Save') }}
                                    </a>
                                    <a href="javascript:;" class="btn btn-danger formbtn widthhalf" v-on:click="Close()"
                                        :title="$t('Cancel')">
                                        <i class="fa fa-close pr-2"></i> {{ $t('Cancel') }}
                                    </a>
                                </div>
                                <!-- <div class="col-lg-6">
                                    <div class="py-2">@HtmlHelpers.MandatoryStringFull("R")</div>
                                </div> -->
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </section>
</template>




<script>


import { Field, GetValidation, errors } from "@/vee-validate"
import DataServices from "@/services/DataService";
import { DatePicker } from "v-calendar/lib/v-calendar.umd";



export default {
    components: {
        DatePicker
    },
    props: {
        isforshow: {
            type: Boolean,
            required: true
        },
        formIdForShow: {
            type: String,
            required: true
        },
        moduleName: {
            type: String,
            required: true
        },
        subModuleCode: {
            type: String,
            required: true
        },
        workOrderId: {
            type: String,
            required: true
        },
        subStatusId: {
            type: String,
            required: true
        },
        listForSubmittedForm: {
            type: Array,
            required: true
        },
        propFormId:{
            required: true
        },
        callbackfunction: {
            type: Function
        }

    },
    data() {
        return {
            WorkOrderId: 0,
            FormId: null,
            FormFieldData: null,
            isLoading: false,
            GroupsOfFields: [],
            ApiURL: null,
            UserType: '',
            containername: ''
            // DependentOption: [{
            //     format: String(Globalize.culture().calendar.patterns.d).toUpperCase(),
            //     useCurrent: false,
            //     showClear: true,
            //     showClose: true,
            //     allowInputToggle: true,
            //     minDate: false//new Date(Date.parse('@DateTime.Now.ToString("MM/dd/yyyy")'))
            // }]

        }
    },
    created() {
        var vm = this;
        vm.UserType = vm.GetUserInfo.UserType;
        vm.containername = vm.GetUserInfo.ContainerName;
        vm.FormId = vm.formIdForShow;

        vm.FetchData();
    },
    methods: {
        makeCheckBoxSelected: function (selectedValue, checkValue, type) {
            if (typeof selectedValue == "string") {
                selectedValue = selectedValue.split(",");
            }
            if (type == 'toggle') {
                if (selectedValue[1].value == checkValue) {
                    return true;
                } else {
                    return false;
                }
            } else {
                if (selectedValue.includes(checkValue)) {
                    return true;
                } else {
                    return false;
                }
            }
        },
        onChangeCheckBox: function (item, value, e) {
            debugger;
            if (typeof item.value == "string") {
                item.value = item.value.split(",");
            }
            if (e.target.checked == true) {
                item.value.push(value);
            } else {
                item.value.splice(item.value.indexOf(value), 1);
            }
        },
        Close: function () {
            var vm = this;
            vm.$parent.ShowWorkOrderStatusForm = false;
            vm.$emit("Close");
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            var url = `id=${vm.FormId}&moduleName=${vm.moduleName}&subModuleCode=${vm.subModuleCode}&otherData=${vm.workOrderId}&subStatusId=${vm.subStatusId}&isForLog=` + vm.isforshow;
            DataServices.GetFormById(url).then(function (response) {
                debugger;

                if (vm.listForSubmittedForm.length > 0) {
                    var formData = vm.listForSubmittedForm.filter(x => x.formId == vm.FormId && x.formSubnittedId != null && x.hasOwnProperty('SaveFormJSON') == true);
                    if (formData.length > 0) {
                        vm.FormFieldData = formData[0].SaveFormJSON;
                    }
                    else if(vm.formIdForShow==0 || vm.formIdForShow=='0'){
                        vm.FormFieldData=vm.listForSubmittedForm;
                    }
                    else {
                        vm.FormFieldData = response.data.data;
                    }

                }
                else {
                    vm.FormFieldData = response.data.data;
                }
                if (vm.FormFieldData != null && vm.FormFieldData.length > 0) {
                    $.each(vm.FormFieldData, $.proxy(function (index, itemHeader) {
                        if (itemHeader.dt_code == "img") {
                            var selectedFile1 = {
                                fieldType: "FileUpload",
                                fileName: "",
                                length: "",
                                fileType: "",
                                containerName: vm.containername,
                                id: vm.FormId,
                                bass64: itemHeader.value
                            }
                            itemHeader.value = selectedFile1;
                        }
                        if (itemHeader.dt_code == "checkbox") {
                            itemHeader.select_value = [];
                        }
                        var headerId = itemHeader.headerId;
                        var CheckHeader = $.grep(vm.GroupsOfFields, function (element, index) {
                            return element.headerId == headerId;
                        });
                        if (CheckHeader.length == 0) {
                            var LayoutObject = {
                                headerId: itemHeader.headerId,
                                header_name: itemHeader.HeaderName,
                                header_DisplayOrder: itemHeader.header_DisplayOrder,
                                headerPositionType: itemHeader.headerPositionType,
                                SectionData: []
                            }
                            var returnAllGroupOfHeader = $.grep(vm.FormFieldData, function (element, index) {
                                return element.headerId == headerId;
                            });
                            $.each(returnAllGroupOfHeader, $.proxy(function (index, itemGroup) {
                                var groupId = itemGroup.group_id;
                                var chkGroup = $.grep(LayoutObject.SectionData, function (element, index) {
                                    return element.group_id == groupId;
                                });
                                if (chkGroup.length == 0) {
                                    var returnAllFieldsOfGroup = $.grep(returnAllGroupOfHeader, function (element, index) {
                                        return element.group_id == groupId;
                                    });
                                    var objGroup = {
                                        group_id: groupId,
                                        group_name: itemGroup.group_name,
                                        LayoutType: itemGroup.layout_type,
                                        group_display_name: vm.GetLocalizedValue(itemGroup.group_name, itemGroup.group_display_name),
                                        group_is_system_generated: itemGroup.group_is_system_generated,
                                        FormFieldData: returnAllFieldsOfGroup
                                    };
                                    LayoutObject.SectionData.push(objGroup);
                                }
                            }, this));
                            vm.GroupsOfFields.push(LayoutObject);
                            console.log("vm.GroupsOfFields", vm.GroupsOfFields);
                        }
                    }, this));
                }                
                setTimeout(function () { vm.RadioButtonBootstrap() }, 100);
                // setTimeout(function () { vm.ShowPopOver() }, 100);
                // vm.$validator.reset();
                setTimeout(function () {
                    vm.isLoading = false;
                    // vm.DependentOption[0].minDate = new Date(Date.parse(vm.currentdate));
                }, 500);
            });
        },
        SubmitData: function (e) {
            var vm = this;
            // vm.$validator.validate().then(function (valid) {
            //     vm.vSelectValidate = true;
            //     if (valid) {
            vm.submited = true;
            vm.isLoading = true;
            // showLoader();
            var fullJson = vm.FormFieldData;
            var data = [];
            var Isvalidcnt = 0;
            $.each(fullJson, function (key, item) {
                var fieldValue = item.value;
                if (item.dt_code == "date") {
                    fieldValue = item.value;
                }
                if (item.name == "contact_number" || item.name == "home_phone" || item.name == "business_phone") {
                    var datalist = vm.phoneList.filter(v => v.field_id == item.custom_field_id);
                    if (datalist.length > 0) {
                        item.value = $.trim(datalist[0].value);
                        fieldValue = $.trim(datalist[0].value);
                        if (datalist[0].isValid == false && $.trim(item.value).length > 0) {
                            Isvalidcnt++;
                        }
                    }
                }
                if (item.dt_code == "checkbox") {
                    fieldValue = String(item.value);
                }
                if (item.dt_code == "select") {
                    try {
                        fieldValue = (typeof fieldValue === 'object' && fieldValue !== null) ? fieldValue.value : fieldValue
                    } catch (ex) { }
                }
                var dataObject = {
                    field_name: item.name,
                    field_value: fieldValue,
                    custom_field_id: item.custom_field_id,
                    dt_code: item.dt_code
                };
                console.log("dataObject1", dataObject);
                data.push(dataObject);
            });
            if (Isvalidcnt > 0) {
                vm.isLoading = false;
                vm.submited = false;
                return false;
            }
            var postJSON = {
                data: data,
                formId: vm.FormId,
                refId: vm.workOrderId,
                subModuleCode: vm.subModuleCode
            };
            var objectPostString = JSON.stringify(postJSON);
            DataServices.SaveUserFormData(objectPostString).then(function (response) {
                debugger;
                // response.headers.get('Expires');
                vm.ShowAlert(response.data.msg, "success", true, vm.$t("Alert"));
                $.each(vm.listForSubmittedForm, function (index, item) {
                    if (item.formId == vm.FormId) {
                        item.formSubnittedId = response.data.returnId;
                        item.SaveFormJSON = fullJson;
                    }
                });
                vm.submited = false;
                vm.Close();
                vm.callbackfunction();
                setTimeout(function () {
                    vm.isLoading = false;
                    // vm.DependentOption[0].minDate = new Date(Date.parse(vm.currentdate));
                }, 500);
            });
            //     }
            // });
        },
        AssignToggleValues: function (event, item, array, parentIndex, index) {
            //debugger;
            var vm = this;
            var value = "";
            if (event.target.checked) {
                value = array[1].value

                var chkItem = $.grep(array, function (element, index) {
                    return (String(element.value).toLowerCase() == 'yes' || String(element.value).toLowerCase() == '1' || String(element.value).toLowerCase() == 'true' || String(element.value).toLowerCase() == 'public');
                });
                if (chkItem != null && chkItem.length > 0) {
                    value = chkItem[0].value;
                }
            }
            else {
                value = array[0].value
                var chkItem = $.grep(array, function (element, index) {
                    return (String(element.value).toLowerCase() == 'no' || String(element.value).toLowerCase() == '0' || String(element.value).toLowerCase() == 'false' || String(element.value).toLowerCase() == 'private');
                });
                if (chkItem != null && chkItem.length > 0) {
                    value = chkItem[0].value;
                }
            }
            if (value == "0" || value.toLowerCase() == "no") {
                value = false;
            }
            else if (value == "1" || value.toLowerCase() == "yes") {
                value = true;
            }
            item.value = value;
            vm.showDependentItem(item, value, parentIndex, index);
        },
        handleFileUpload: function ($event, item) {
            var vm = this;
            this.selectedFile = $event.target.files[0];
            var selectedFile1 = {
                fieldType: "FileUpload",
                fileName: $event.target.files[0].name,
                length: $event.target.files[0].size,
                fileType: $event.target.files[0].type,
                containerName: vm.containername,
                id: vm.FormId,
            }
            var reader = new FileReader()
            reader.readAsDataURL(this.selectedFile);
            reader.onload = () => {
                selectedFile1.bass64 = reader.result;
                item.value = selectedFile1;
            };
            console.log("selectedFile1", selectedFile1);
        },
    }
}
</script>
