<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Escalate') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                        <!-- <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button"
                                aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>User Guide</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgAddEditResourceType')"></div>
                                </div>
                            </div>
                        </span> -->
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-TicketEscalateType="{ data }">
                                <div class="custom-control custom-radio custom-control-inline">
                                    <input type="radio" id="TicketEsclateType" name="TicketEsclateType" value="0"
                                        v-model="selectedValue" class="form-check-input rdoTicketEsclateType" />
                                    <label class="mb-0">
                                        {{ $t('With In Company') }}
                                    </label>


                                </div>
                                <div class="custom-control custom-radio custom-control-inline">
                                    <input type="radio" v-on:click="showDepartmentDropdown" id="TicketEsclateType" name="TicketEsclateType" value="1"
                                        v-model="selectedValue" class="form-check-input rdoTicketEsclateType" />
                                    <label class="mb-0">
                                        {{ $t('Parent Company(R.LAW)') }}
                                    </label>

                                </div>
                            </template>
                        </dynamic-form>

                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        ParentCompanyId: {
            type: String,
            required: true
        },
        ticketId: {
            Type: Number,
            required: true
        },
        ParentCompanyName: {
            Type: String,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            TicketId: null,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [

                        {
                            astype: "slotField",
                            label: this.$t('Escalate To'),
                            name: "TicketEscalateType",
                            value: "",
                            config: {
                                options: [],
                            },
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Department Name'),
                            name: "Department",
                            value: "",
                            visibility: false,
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select Department'),
                            validationRules: { "required": true, max: 100 },
                        }
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Reason'),
                            name: "Reason",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 200 },
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Submit",
                    // onClick: this.onSubmit
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ]
        }
    },
    created: async function () {
        var vm = this;
        vm.GetDepartmentListbyCompanyForClient();
    },
    methods: {
        showDepartmentDropdown() {
            debugger;
            // Method to handle displaying department dropdown
           var vm = this;
           vm.FormSchema[0].Data[1].visibility = true;
        },
        Close: function () {
            this.$parent.isShowEscalate = false;
        },
        GetDepartmentListbyCompanyForClient: async function () {
            debugger;
            var vm = this;
            await DataServices.GetDepartmentListbyCompanyForClient().then(async (response) => {
                if (response.data.length > 0) {
                    vm.DepartmentList = response.data;
                    vm.DepartmentList.forEach((item, index) => {
                        (vm.FormSchema)[0].Data[1].config.options.push({
                            value: item.departmenT_ID,
                            name: item.departmenT_NAME
                        })
                    })
                }
            });
        },
        onSubmit(formfields, val) {
            debugger;
            var vm = this;
            var i = 0;
            // console.log(this.selectedValue);
            var jsonobj = {
                TicketId: vm.ticketId,
                TicketGroupId: 0,
                TicketEsclateId: 0,
                TicketEsclateReason: vm.FormSchema[1].Data[0].value,
                TicketEsclateType: this.selectedValue,
                ParentCompanyId: vm.ParentCompanyId,
                TicketEsclateDepartment: vm.FormSchema[0].Data[1].value,
                TicketbBoardStatus: 'Escalate'
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveTicketEsclate(params).then((response) => {

                if (response.data.status == 'Success') {
                    if (response.data.result.STATUSCODE == 1) {
                        vm.ShowAlert(vm.$t("Ticket is escalated successfully."), "success", true, vm.$t("Alert"));
                        vm.Close();
                    }
                    else if (response.data.result.STATUSCODE == null) {
                        vm.ShowAlert(vm.$t("Next Higher Level Doesn't Exist."), "failure", true, vm.$t("Alert"));
                    }
                    else {
                        vm.ShowAlert(vm.$t("Escalated CAn't Saved."), "failure", true, vm.$t("Alert"));
                    }
                    vm.Close();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Resource")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>