<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full d-flex">
                            <h5 class="modal-title">{{ $t('Merge') }}</h5>
                            <button type="button" class="close" v-on:click="Close">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                            <span class="user-guide">
                                <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG"
                                    role="button" aria-expanded="false" aria-controls="collapseExample"> <em
                                        class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                                <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                    <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                        <div class="con" v-html="$t('UgAddTicketMerged')"></div>
                                    </div>
                                </div>
                            </span>
                        </div>
                        <div class="modal-body">
                            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                                v-on:OnSubmit="UploadFileWithMerge">
                                <template v-slot:tgslot-Description="{ data }">
                                    <ckeditor :editor="editor" @ready="onReady" v-model="data.fieldInfo.value">
                                    </ckeditor>
                                    <span name="Description" class="invalid-feedback d-block">{{ data.error }}</span>
                                    <!-- {{ data.fieldInfo }} -->
                                </template>

                            </dynamic-form>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </section>
</template>

<script>
import DataServices from "@/services/DataService";
//import ClassicEditor from '@ckeditor/ckeditor5-build-classic';

import { Field, getValidationRules } from "@/vee-validate/index"

export default {
    props: {
        id: {
            type: Number,
            required: false
        },
        str_ticket_id: {
            type: String,
            required: true
        },
        modulename: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
        callbackfunction: {
            type: Function
        }
    },
    data() {
        return {
            editor: this.CLASSIC_EDITOR,
            content: "<h1>Some initial content</h1>",
            isLoading: false,
            search: '',
            TicketBindList: [],
            Description: null,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "MultiSelectField",
                            mode: "tag",
                            label: this.$t('CombineTicket'),
                            name: "MergeTicket",
                            value: "",
                            config: {
                                options: [],
                                onSearchChange: this.onTicketType,
                                onChange: this.onChangeCombinedTicket
                            },
                            placeholder: this.$t('SelectTicket'),
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('PrimaryTicket'),
                            name: "PrimaryTicket",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('SelectPrimaryTicket'),
                            validationRules: { "required": true, max: 100 },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('Reason'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            settings: {
                                isInSlot: true
                            },
                            visibility: true,
                            validationRules: { "required": true, max: 200 },
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ]
        }
    },
    created: async function () {
        // this.onTicketType();
    },
    methods: {
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },

        onTicketType: async function (x, y, z) {
            debugger;
            var vm = this;
            vm.search = z;
            var url = `ticketId=${vm.str_ticket_id}&search=${vm.search}`;
            await DataServices.GetTicketListData(url).then(function (response) {
                if (response.data != null && response.data != "") {
                    var json = response.data;
                    if (json != null) {
                        response.data.forEach((item, index) => {
                            vm.FormSchema[0].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        onChangeCombinedTicket: async function (e, x, y) {
            var vm = this;
            debugger;
            vm.FormSchema[0].Data[1].config.options.push({
                value: y.value,
                name: y.label
            })
        },
        UploadFileWithMerge: async function (formfields, val) {
            var vm = this;
            ////debugger;
            var CombinedTicketIds = '';
            var i = 0;
            for (i = 0; i < vm._data.FormSchema[0].Data[0].value.length; i++) {
                if (i == 0) {
                    CombinedTicketIds = vm._data.FormSchema[0].Data[0].value[i].value;
                } else {
                    CombinedTicketIds = CombinedTicketIds + "," + vm._data.FormSchema[0].Data[0].value[i].value;
                }
            }
            var jsonobj =
            {
                TicketId: vm.id,
                CombinedTicketIds: CombinedTicketIds,
                PrimaryTicketId: vm._data.FormSchema[0].Data[1].value,
                Reason: vm._data.FormSchema[1].Data[0].value

            };
            var params = JSON.stringify(jsonobj);
            await DataServices.SaveMergeTicket(params).then((response) => {
                ////debugger;
                if (response.data.status == 'Success') {
                    if (response.data.url != "") {

                        var message = [vm.$t("TICKET_SAVED")] + " " + [vm.$t("RedirectToMerge")] + " #" + response.data.PrimaryTicket;
                        vm.ShowAlert(message, "success", true, vm.$t("Alert"));
                        window.location.href = "/Ticketing/Manage/" + response.data.url;
                    }
                    else {
                        var message = [vm.$t("TICKET_SAVED")] + " " + [vm.$t("RedirectToMerge")] + " #" + response.data.PrimaryTicket;
                        vm.ShowAlert(message, "success", true, vm.$t("Alert"));
                        vm.Close();
                        vm.callbackfunction();

                    }
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("TICKET_MERGE")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        },
        Close: function () {
            var vueObj = this;
            vueObj._data.FormSchema[0].Data[1].value = null;
            vueObj._data.FormSchema[1].Data[0].value = null;
            vueObj._data.FormSchema[0].Data[0].value = null;
            vueObj.$parent.isShowMergeTicket = false;
        },
    }
}
</script>