<template>
    <section>
        <tg-list ModuleName="Ticketing" SubModuleCode="Ticketing"
                                    IdentityColumn="TICKET_ID" :listType="['List']" :ListData="RelatedTicketListing"
                                    :HeaderData="Headers" 
                                    :TotalRecords="TotalRecords"
                                    :IsShowAction="true" 
                                    :RenderRowActionMethod="() => { }"
                                    :ListDataCallBackFunction="FetchRecords"
                                    :NorecordfoundText="$t('NoRecordfound')"
                                     v-on:PagerButtonClick="pagerMethod" v-on:SortdataButtonClick="sortdata">
                                     <template v-slot:slotdata="{ data }">
                                        
                                       <template v-if="data.column.COLUMN_NAME == 'CREATED_AT'">
                                           <span>
                                               {{ $options.filters.formatDateTime(data.row.CREATED_AT) }}
                                           </span>
                                       </template>
                                       <template v-if="data.column.COLUMN_NAME == 'TICKET_NUMBER'">
                                         <RouterLink :to="'/Ticketing/Manage/'+data.row.str_ticket_id" :title="data.row[data.column.COLUMN_NAME]"  data-toggle-tooltip="tooltip" data-original-title="Click here to view Ticket Detail">
                                          {{ data.row[data.column.COLUMN_NAME] }}
                                           </RouterLink>
                                        </template>
                                   </template>
        </tg-list>
    </section>
</template>
<script>

import DataServices from "@/services/DataService";
export default {
    props:{
        ticketId: {
            type: Number,
            required: false
        }
    },
    data() {
        return {
            noRecords: false,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            Headers: [{
                "COLUMN_NAME": "TICKET_NUMBER",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Ticket Number",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "TICKET_SUBJECT",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Subject",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "CDC",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "CDC",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "CREATED_AT",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created At",
                "DISPLAY_ORDER": 4
            }

            ],
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            GroupStart: 1,
            GroupEnd: 3,
            RelatedTicketListing: []
           
        }
    },
    created: async function () {
        var vm = this;
        await vm.FetchRecords();
    },
    methods: {
        FetchRecords: async function () {
            debugger;
            var vueObj = this;
            vueObj.RelatedTicketListing = [];
            var url = `sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}&lang=EN&parentticketId=`+ vueObj.ticketId;
            await DataServices.GetRelatedTicketList(url).then(function (response) {
                debugger;
                vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == 'CREATED_AT') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'TICKET_NUMBER') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                    });
                if (response.data != "" && response.data != null) {
                    if (response.data.result.DATA.length > 0) {                        
                        vueObj.RelatedTicketListing = response.data.result.DATA;
                        vueObj.TotalRecords = response.data.result.DATA[0].TOTALRECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                        vueObj.IsLoad = true;
                    } else {
                        vueObj.noRecord = true;                     
                        vueObj.TotalRecords = 0   
                        vueObj.RelatedTicketListing=[];                  
                    }
                }
                else {
                    vueObj.noRecord = true;                                
                    vueObj.TotalRecords = 0
                    vueObj.RelatedTicketListing=[];   
                }               
                setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
            })
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        }
       
    },
}
</script>