<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full  d-flex">
                            <h4 class="modal-title">{{ $t('UpdateStatus') }}</h4>
                            <button type="button" class="close" v-on:click="Close">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <dynamic-form lang="en" :schema="locateFormSchema" v-if="isLocateorNonLocate"
                                v-on:OnSubmit="onSubmit"></dynamic-form>

                            <dynamic-form lang="en" :schema="nonLocateFormSchema" v-if="!isLocateorNonLocate"
                                v-on:OnSubmit="onSubmit"></dynamic-form>
                        </div>
                        <div class="col-md-12" v-if="formListForSubmit.length > 0">
                            <div class="listing">
                                <div class="table-responsive table-fix-header">
                                    <table class="table table-bordered dt-responsive nowrap mt-0" id="tbl_formList">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th class="sorting"><span>{{ $t('FormName') }}</span></th>
                                                <th class="text-center"><span>{{ $t('FormSubmitted') }}</span></th>
                                                <th class="text-center sorting"><span>{{ $t('Action') }}</span></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(row, index) in formListForSubmit">
                                                <td>
                                                    {{ row.name }} <span class="text-danger font-weight-bold"
                                                        v-if="row.second_value == '1'">*</span></td>
                                                <td class="text-center">
                                                    <span class="px-2 bg-success text-white text-center"
                                                        v-if="checkFormSubmitted(row.value) == 'Yes'">{{
                                                            checkFormSubmitted(row.value) }}</span>
                                                    <span class="px-2 bg-danger text-white text-center"
                                                        v-if="checkFormSubmitted(row.value) == 'No'">{{
                                                            checkFormSubmitted(row.value) }}</span>
                                                </td>
                                                <td class="text-center" v-if="EditEnabled == false"><a
                                                        href="javascript:;"><i
                                                            class="fa fa-edit fa-lg action-icon text-success disabled"></i></a>
                                                </td>
                                                <td class="text-center" v-if="EditEnabled"><a href="javascript:;"
                                                        v-on:click="GetCustomFieldFormForEdit(row)"><i
                                                            class="fa fa-edit fa-lg action-icon text-success"></i></a></td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                        <div class="modal-footer col-12 px-0 pb-0">
                            <dynamic-form lang="en" :buttons="locateButtons" v-if="isLocateorNonLocate"
                                v-on:OnSubmit="SubmitUpdateStatus"></dynamic-form>

                            <dynamic-form lang="en" :buttons="nonLocateButtons"
                                v-if="!isLocateorNonLocate && formListForSubmit.length > 0"
                                v-on:OnSubmit="SubmitUpdateStatus"></dynamic-form>

                            <dynamic-form lang="en" :buttons="nonLocateWorkOrderStatusButtons"
                                v-if="!isLocateorNonLocate && formListForSubmit.length == 0"
                                v-on:OnSubmit="SubmitUpdateStatus"></dynamic-form>
                        </div>

                    </div>
                </div>
            </div>
        </div>
        <EditViewFSMForm :isforshow="false" :formIdForShow="formIdForShow" :listForSubmittedForm="listForSubmittedForm"
            :moduleName="moduleName" :subModuleCode="subModuleCode" :workOrderId="workOrderId" :subStatusId="subStatusId"
            :callbackfunction="callbackfunction" v-if="ShowWorkOrderStatusForm"></EditViewFSMForm>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
import EditViewFSMForm from "../../components/Common/ViewFSMForm.vue";
export default {
    components: {
        EditViewFSMForm
    },
    props: {
        updateWorkOrderStatus: {
            type: Array,
            required: true
        },
        isLocateorNonLocate: {
            type: String,
            required: true
        },
        callbackfunction: {
            type: Function
        }
    },
    data() {

        return {
            workOrderTicketId: 0,
            workOrderId: 0,
            isLoading: false,
            ShowWorkOrderStatusForm: false,
            ChildSubStatusId: '',
            ParentSubStatusId: '',
            WorkOrderStatusId: '',
            ChildSubStatusName: '',
            moduleName: 'TICKETING',
            subModuleCode: 'TICKETING',
            ParentSubStatusName: '',
            Description: '',
            MinutesWorked: '',
            formListForSubmit: [],
            listForSubmittedForm: [],
            workorderStatusDataList: [],
            formIdForShow: null,
            subStatusId: 0,
            FeetLocated: '',
            EditEnabled: false,
            IsAction: false,
            locateFormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            mode: "tag",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.SetParentSubStatus
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('SubStatus'),
                            name: "SubStatus",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.GetFormsOnBehalfOfSubStatus
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "NumericField",
                            label: this.$t('MinutesWorked'),
                            name: "Minutes Worked",
                            value: "",
                            validationRules: { "required": false },
                        },
                        {
                            astype: "NumericField",
                            label: this.$t('FeetLocated'),
                            name: "FeetLocated",
                            value: "",
                            validationRules: { "required": false },
                        },

                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Comment'),
                            name: "Comment",
                            value: "",
                            validationRules: "",
                        },
                    ]
                }
            ],
            locateButtons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
            nonLocateFormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            mode: "tag",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.onChangeStatus
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        }
                    ],
                }
            ],
            nonLocateButtons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
            nonLocateWorkOrderStatusButtons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Update Status",
                    onClick: this.WorkOrderUpdateStatus
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ]
        }
    },
    created: async function () {
        var vm = this;
        //////debugger;
        vm.workOrderTicketId = vm.updateWorkOrderStatus.ticket_id;

        vm.workOrderId = vm.updateWorkOrderStatus.id;
        if (vm.updateWorkOrderStatus.WO_Status_Id != '') {
            vm.WorkOrderStatusId = vm.updateWorkOrderStatus.WO_Status_Id;
        }
        vm.GetDropDownData('WORK_ORDER_FINAL_STATUS');
        vm.GetUpdateStatusDataForEdit(vm.workOrderId, vm.workOrderTicketId);
        if (vm.isLocateorNonLocate == true) {
            vm.GetFSMDDLData("FSM_WORK_ORDER", vm.workOrderTicketId, vm.workOrderId);
        }
    },
    methods: {
        GetCustomFieldFormForEdit: function (item) {
            var vm = this;
            vm.formIdForShow = item.value;
            vm.subStatusId = vm._data.locateFormSchema[0].Data[1].value;
            vm.ShowWorkOrderStatusForm = !vm.ShowWorkOrderStatusForm;
        },
        onChangeStatus: function (event) {
            //////debugger;
            var vm = this;
            vm.WorkOrderStatusId = event.target.value;
            let selectedStatus = vm.workorderStatusDataList.find(x => x.value == vm.WorkOrderStatusId);
            if ((selectedStatus.name == "Cancelled" || selectedStatus.name == "Completed") && vm.IsAction == true) {
                vm.EditEnabled = false;
            }
            else {
                vm.EditEnabled = true;
            }
        },
        checkFormSubmitted: function (formId) {
            var vm = this;
            var returnValue = 'No';
            $.each(vm.listForSubmittedForm, function (index, item) {
                if (item.formId == formId && item.formSubmittedId > 0) {
                    returnValue = 'Yes';
                }
            });
            return returnValue;
        },
        GetUpdateStatusDataForEdit: async function (WorkOrderId, WorkOrderTicketId) {
            //////debugger;
            var vueObj = this;
            var param = `TicketId=${WorkOrderTicketId}&WOId=${WorkOrderId}`;
            await DataServices.GetSingleWorkOrder(param).then(function (response) {
                //////debugger;
                if (response.data.status == "Success") {

                    var json = response.data.result
                    if (json != null) {
                        if (vueObj.isLocateorNonLocate == true) {
                            vueObj.ParentSubStatusId = json.PARENT_SUB_STATUS_ID == null ? '' : json.PARENT_SUB_STATUS_ID;
                            vueObj.ChildSubStatusId = json.SUB_STATUS_ID == null ? '' : json.SUB_STATUS_ID;
                            vueObj.WorkOrderStatusId = json.STATUS_ID == null ? '' : json.STATUS_ID;
                            vueObj.ParentSubStatusName = json.PARENT_SUB_STATUS_NAME;
                            vueObj.ChildSubStatusName = json.CHILD_SUB_STATUS_NAME;
                            vueObj.Description = json.COMMENT;
                            vueObj.SetParentSubStatus(WorkOrderId);

                            vueObj.MinutesWorked = json.MINUTES_WORKED > 0 ? json.MINUTES_WORKED : '';
                            vueObj.FeetLocated = json.FEET_LOCATED > 0 ? json.FEET_LOCATED : '';
                            if (vueObj.ChildSubStatusId > 0) {
                                vueObj.GetFormsOnBehalfOfSubStatus();
                            }
                        }
                        else {
                            vueObj._data.nonLocateFormSchema[0].Data[0].value = vueObj.WorkOrderStatusId;
                            vueObj.GetFormsWithoutSubStatus();
                        }
                    }
                }
            });
        },
        SetParentSubStatus: function () {
            //debugger;
            var vueObj = this;
            vueObj.GetFSMDDLData("FSM_WORK_ORDER_CHILD_SUB_STATUS", vueObj.workOrderTicketId, vueObj.workOrderId);
            vueObj.GetFormsOnBehalfOfSubStatus(vueObj._data.locateFormSchema[0].Data[0].value);
        },
        GetFSMDDLData: async function (type, workOrderTicketId, workOrderId) {
            //debugger;
            var vm = this;
            var json = "";
            var refId = "";
            if (type == "FSM_WORK_ORDER") {
                refId = "";
            }
            refId = vm._data.locateFormSchema[0].Data[0].value;
            vm._data.locateFormSchema[0].Data[1].config.options = [];

            var param = "id=" + workOrderTicketId + "&moduleName=&type=" + type + "&refId=" + refId + "&workOrderId=" + workOrderId;
            await DataServices.GetFSMDDLData(param).then(function (response) {
                //debugger;
                if (!vm.isBlank(response.data) && response.data.status == "Success") {
                    json = response.data.result.DATA;
                    if (json != null) {
                        if (type == "FSM_WORK_ORDER") {
                            json.forEach((item, index) => {
                                vm._data.locateFormSchema[0].Data[0].config.options.push({
                                    value: item.value,
                                    name: item.name
                                })
                            })

                        }
                        else if (type == "FSM_WORK_ORDER_CHILD_SUB_STATUS") {
                            json.forEach((item, index) => {
                                vm._data.locateFormSchema[0].Data[1].config.options.push({
                                    value: item.value,
                                    name: item.name
                                })
                            })

                        }
                    }
                    else {
                        if (type == "FSM_WORK_ORDER_CHILD_SUB_STATUS") {
                            vm._data.locateFormSchema[0].Data[1].value = "";
                        }
                    }
                }
            });
        },
        GetFormsWithoutSubStatus: async function () {
            ////debugger;
            var vm = this;
            vm.formListForSubmit = [];
            var param = `id=${vm.workOrderId}&moduleName=TICKETING&type=FSM_FORMS_WITHOUT_STATUS_FOR_NON_LOCATE&search=&refId=`;
            await DataServices.GetDDLData(param).then(function (response) {
                ////debugger;
                if (!vm.isBlank(response.data) && response.data.DATA.length > 0) {
                    vm.formListForSubmit = response.data.DATA;
                    $.each(response.data.DATA, function (index, item) {
                        var obj = {
                            formId: item.value,
                            formSubmittedId: item.first_value,
                            isAllFormMandatory: item.second_value
                        }
                        vm.listForSubmittedForm.push(obj);
                    });
                }
            });
        },
        WorkOrderUpdateStatus: async function () {
            ////debugger;
            var vm = this;
            var jsonobj =
            {
                Id: vm.workOrderId,
                StatusId: vm.WorkOrderStatusId
            }
            var params = JSON.stringify(jsonobj);
            await DataServices.MarkAsDone(params).then(function (response) {
                ////debugger;
                if (response.data == 1) {
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Status")]), "success", true, vm.$t("Alert"));
                    vm.Close();
                    vm.callbackfunction();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Status")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        },
        GetFormsOnBehalfOfSubStatus: async function (parentStatusCodeId) {
            //debugger;
            var vm = this;
            let refId = vm._data.locateFormSchema[0].Data[1].value;
            // parentStatusCodeId = vm._data.locateFormSchema[0].Data[0].value;
            // if (parentStatusCodeId && parentStatusCodeId != null) {
            //     refId = parentStatusCodeId
            // }
            vm.formListForSubmit = [];
            vm.listForSubmittedForm = [];
            var param = `id=${vm.workOrderId}&moduleName=TICKETING&type=FSM_FORMS_BY_STATUS&search=&refId=` + refId;
            await DataServices.GetDDLData(param).then(function (response) {
                if (!vm.isBlank(response.data) && response.data.DATA.length > 0) {
                    vm.formListForSubmit = response.data.DATA;
                    $.each(response.data.DATA, function (index, item) {
                        var obj = {
                            formId: item.value,
                            formSubmittedId: item.first_value,
                            isAllFormMandatory: item.second_value
                        }
                        vm.listForSubmittedForm.push(obj);
                    });
                }
            });
        },
        GetDropDownData: async function (type) {
            var vm = this;
            var param = `id=&moduleName=Ticketing&type=${type}&search=&refId=`;
            vm.workorderStatusDataList = [];
            await DataServices.GetDDLData(param).then(function (response) {
                //////debugger;
                if (response.data.DATA.length > 0) {
                    var json = response.data.DATA;
                    vm.workorderStatusDataList = response.data.DATA;
                    if (json != null) {
                        response.data.DATA.forEach((item, index) => {
                            vm._data.nonLocateFormSchema[0].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                    let selectedStatus = json.find(x => x.value == vm.WorkOrderStatusId);
                    if (selectedStatus.name == "Cancelled" || selectedStatus.name == "Completed") {
                        vm.EditEnabled = false;
                        vm.IsAction = true;
                    }
                    else {
                        vm.EditEnabled = true;
                    }
                }
            });
        },

        Close: function () {
            this.$parent.showWorkOrderStatusModal = false;
        },


        SubmitUpdateStatus(formfields, val) {
            ////debugger;
            var vueObj = this;
            let selectedStatus = vueObj.workorderStatusDataList.find(x => x.value == vueObj.WorkOrderStatusId);
            if (selectedStatus.name != "Cancelled") {
                $.each(vueObj.listForSubmittedForm, function (index, item) {
                    if ((item.formSubnittedId == null || item.formSubnittedId == 0) && item.isAllFormMandatory == "1") {
                        IsFormValid = false;
                    }
                });
                //}
                $.each(vueObj.listForSubmittedForm, function (index, item) {
                    if (item.hasOwnProperty('SaveFormJSON') == true) {
                        delete item.SaveFormJSON;
                    }
                });
            }
            var jsonobj = {
                WorkOrderId: vueObj.workOrderId,
                TicketId: vueObj.workOrderTicketId,
                ParentSubStatusId: vueObj._data.locateFormSchema[0].Data[0].value,
                ChildSubStatusId: vueObj._data.locateFormSchema[0].Data[1].value,
                Description: vueObj._data.locateFormSchema[2].Data[0].value,
                IsCornerLot: 0,
                IsGasfacility: 0,
                IsCastIron: 0,
                IsPrivateLocateOpportunity: 0,
                IsGasPrintIssue: 0,
                NumberofCIMains: '',
                MainSizeAndDistance: '',
                FormSaveId: 0,
                formBaseString: vueObj.listForSubmittedForm,
                MinutesWorked: vueObj._data.locateFormSchema[1].Data[0].value,
                FeetLocated: vueObj._data.locateFormSchema[1].Data[1].value,
                ReqFrom: 'WEB',
                WorkOrderStatusId: vueObj.WorkOrderStatusId
            };
            var params = JSON.stringify(jsonobj);
            DataServices.UpdateSubStatus(params).then((response) => {
                ////debugger
                if (response.data.result.MSG == 'SUCCESS') {
                    vueObj.ShowAlert(vueObj.$t("UpdatedSuccess", [vueObj.$t("Status")]), "success", true, vueObj.$t("Alert"));
                    vueObj.Close();
                    vueObj.callbackfunction();
                }
                else {
                    vueObj.ShowAlert(vm.$t("ErrorWhileSavingRecords", [vueObj.$t("Status")]), "failure", true, vueObj.$t("Alert"));
                    vueObj.Close();
                }
            });
        }
    }
}
</script>