<template>
    <div class="my-popups">
        <!-- <loader :is-visible="isLoading"></loader> -->
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title"> Follow Up </h5>
                        <button v-on:click="$emit('close')" href="javascript:;" class="close"><em class="fa fa-times"
                                aria-hidden="true"></em></button>
                                <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse"  role="button"
                                aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgTicketingFollowupAdd')"></div>
                                </div>
                            </div>
                        </span>
                    </div>
                    <div class="modal-body" v-if="ticketStatus != 'ticket_closed' && ticketStatus != 'ticket_closedlocked'">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="SaveFollowup">

                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from '../../services/DataService';

export default {

    props: {
        ticketStatus: {
            type: String,
            required: true
        },
        sourceid: {
            Type: String,
            required: true
        },
        followUpId: {
            Type: String,
            required: true
        },
        followupListById: {
            Type: Array,
            required: true
        }
    },
    data: function () {
        return {
            ScheduleDate: new Date(),
            time: '',
            Comment: '',
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "DateField",
                            label: this.$t('Date(M/D/YYYY)'),
                            name: "Date",
                            value: '',
                            placeholder: "",
                            config: { format: "MM/DD/YYYY" },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('Time(hh:mm A)'),
                            name: "Time",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },
                            config: {
                                mode: "time",                                
                                format: "hh:mm A",
                                //minDate: (new Date())
                                //maxDate: ....
                            }
                        }
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Comment'),
                            name: "Comment",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 200 },
                        },
                    ],
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ],
        }
    },
    created() {
        var vm = this;
        vm.edit();
    },
    methods: {
        SaveFollowup: function () {
            //debugger;

            var vm = this;
            var obj = {
                ScheduleDate: vm.$options.filters.formatDate(vm.FormSchema[0].Data[0].value),
                time: vm.$options.filters.formatTime(vm.FormSchema[0].Data[1].value),
                FollowUpDescription: vm.FormSchema[1].Data[0].value,
                ticketId: vm.sourceid,
                FollowUpId: vm.followUpId
            }
            var objectPostString = JSON.stringify(obj);
            DataServices.AddFollowUp(objectPostString).then((response) => {
                //debugger
                if (response.data.msg != null) {
                    vm.ShowAlert(response.data.msg, "failure", true, vm.$t("Alert"));
                }
                else if (response.data.newId != null) {
                    vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('FollowUp')]), "success", true, vm.$t("Alert"));
                    vm.$parent.loadFollowUpData(vm.sourceid, vm.ticketStatus);
                    vm.$emit('close');
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", response.data.msg), "failure", true, vm.$t("Alert"));
                }

            })
        },
        edit: function () {
            //debugger
            var vm = this;
            var dateParts = vm.followupListById.scheduleDate.split('-'); 
            var formattedDate = dateParts[2] + '-' + dateParts[1] + '-' + dateParts[0];

            var timeParts = vm.followupListById.time.split(':'); // Assuming time is in "HH:MM" format
            var hours = parseInt(timeParts[0], 10);
            var minutes = parseInt(timeParts[1], 10);

            var combinedDateTime = new Date(dateParts[0], dateParts[1] - 1, dateParts[2], hours, minutes);
            vm.FormSchema[1].Data[0].value = vm.followupListById.followUpDescription;
            vm.FormSchema[0].Data[0].value = formattedDate;
            vm.FormSchema[0].Data[1].value = combinedDateTime;
        },
        Close: function () {
            this.$parent.showAddEventModalForAddEdit = false;
        },
    }
}
</script>
