<template>
    <section>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('TimeTrack') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"  v-on:OnSubmit="UploadFileWithTime">                            
                             <template v-slot:tgslot-Description="{ data }">
                                <ckeditor :editor="editor" @ready="onReady" v-model="data.fieldInfo.value" ></ckeditor>
                                <span name="Description" class="invalid-feedback d-block">{{ data.error }}</span>
                                <!-- {{ data.fieldInfo }} -->
                            </template>
                            
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
    
</section>
</template>

<script>
import DataServices from "@/services/DataService";
//import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import { Field, getValidationRules } from  "@/vee-validate/index" 
export default {
    props: {      
        id: {
            type: Number,
            required: false
        }, 
        modulename: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
        callbackfunction: {
            type: Function
        },
        viewMode : {
            type: String,
            required: true
        }
    },     
    data() {
        return {
            editor: this.CLASSIC_EDITOR,
            content: "<h1>Some initial content</h1>",
            isLoading: false,
            search:'',
            TicketBindList:[],
            Description:null,
            Disabled:0,
            FormSchema: [
               {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('WorkOrder'),
                            name: "WorkOrder",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                            {
                            astype: "SlotField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                              },
                              settings: {
                                isInSlot: true
                            },
                            visibility: true,
                            validationRules: { "required": true, max: 200 },  
                           },                                       
                    ]
                }
            ],
             buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ]
        }
    },   
    created: async function () {    
        this.GetDDLData("FSM_TRACK_HISTORY");
    },
    methods: { 
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );  
        },
        GetDDLData: async function (type) {
            //debugger;
                    var vm = this;
                    var url=`id=${vm.id}&moduleName='FSM'&type=`+type;

                    await DataServices.GetDDLDataFSM(url).then(function (response) {
                    if(response.data.status == "Success")
                    {
                        //debugger;
                        response.data.result.DATA.forEach((item, index) => {
                             vm.FormSchema[0].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                           })
                       })
                    }
                });
            },
     
            UploadFileWithTime:  function (formfields, val) {
            var vm = this;
            //debugger;
            var jsonobj = 
             {
                TicketId : vm.id,
                WorkOrderId: vm._data.FormSchema[0].Data[0].value,
                TrackDescription: vm._data.FormSchema[1].Data[0].value,
                IsBillable: true,
                TrackId: 0,
                RequestFor: 'FSM',
                RequestForType: 'START',
                IPAddress: '',
                WorkOrderSubject:  vm._data.FormSchema[0].Data[0].name

             };
              var params = JSON.stringify(jsonobj);
              vm.confirmR(vm.$t('ConfirmTimerMsg'),true, vm.$t('StartTimer'),function(result) {
                vm.Disabled = 1;
                 DataServices.SaveTrackHistory(params).then((response) => {
                    try 
                    {
                     //debugger;
                       if (response.data.status == 'Success') 
                       {
                          vm.$parent.ShowTimer = true;
                          vm.$parent.AddEditText = "Edit";
                          vm.Disabled = 0;
                          vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("TimeTrack")]), "success", true, vm.$t("Alert"));
                          vm.Close();
                       }
                      else 
                      {
                       vm.ShowAlert(vm.$t("AddedError", [vm.$t("TimeTrack")]), "failure", true, vm.$t("Alert"));
                       vm.Disabled = 0;
                       vm.Close();
                      } 
                    }
                    catch (exinner)
                    {
                        return HandleErrorOccured(exinner, 'failure');
                    }
                   });
               });
            },            
             Close: function () {
                var vm = this;
                vm.$emit("Close");
                vm._data.FormSchema[0].Data[0].value = null;
                vm._data.FormSchema[1].Data[0].value = null;
                vm.$parent.isShowWOTimer = false;
                if(vm.viewMode == "Timer")
                {
                    vm.callbackfunction();
                }
             },            
    }
}
</script>