<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <template v-if="NoteList.length > 0">
            <div class="row card-view no-bg custom-box-width">

                <div class="col-md-12 col-lg-6 col-xl-3 mb-3" v-for="(note, index) in NoteList" :key="index">
                    <div
                        :class="{ 'card-box': true, 'green-bg-light': note.PRIVACY != 'Public', 'blue-bg-light': note.PRIVACY == 'Public' }">
                        <div class="card-heading">
                            <span class="user-img-con mr-2">
                                <img v-if="note.AVATAR" class="img-circle avatar" :src="note.AVATAR" :alt="$t('Image')" />
                                <img v-else class="img-circle avatar" src="/Content/images/DefaultImages/avatar-icon.jpg"
                                    :alt="$t('Image')" border="0" />
                            </span>
                            {{ !note.DEPARTMENT_NAME ? '--' : note.DEPARTMENT_NAME }}
                        </div>
                        <div class="card-detail-main1 dynamiclistol">
                            <div class="card-detail"><em class="fa fa-user" aria-hidden="true"></em>
                                <span>{{ note.CREATED_BY_NAME }}</span>
                            </div>
                            <hr class="my-2" />
                            <div class="clearfix"></div>
                            <div class="commentnoteslist" style="max-height:260px;">
                                <div class="w-100 float-left custom-scrollbar-js" v-html="note.NOTE"></div>
                            </div>
                        </div>
                        <div class="bottom-action">
                            <div class="card-date">
                                <em class="fa fa-calendar-o" aria-hidden="true"></em>
                                <span>
                                    {{ !note.CREATED_AT ? '--' : $options.filters.formatDateTime(note.CREATED_AT) }}
                                </span>
                            </div>
                            <div class="card-action">
                                <a v-on:click="DownloadAttachment(note)"
                                    class="round-icon-small btn-primary-light downloadAttachement"
                                    :title="$t('ClickHereDownloadAttachment')" :alt="$t('DownloadFile')" href="javascript:;"
                                    :value="note.TICKET_ATTACHMENT_ID">
                                    <em class="fa fa-download"></em>
                                </a>
                                <a v-if="ListNoteData != 1" v-on:click="EditNote(note)" class="round-icon-small btn-success-light"
                                    :title="$t('EditNotes')" :alt="$t('EditNotes')" href="javascript:;">
                                    <em class="fa fa-edit"></em>
                                </a>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </template>
        <div class="col-sm-12 col-md-12 col-lg-12 mt-2" v-if="NoRecords">
            <span class="text-danger text-center d-block no-record">{{ $t('NoRecordfound') }}</span>
        </div>
    </section>
</template>
            

<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        NoteData: {
            type: Array,
            required: true
        },
        ListNoteData:{
            type:Number,
            required:false
        }
    },
    data() {
        return {
            NoRecords: false,
            isShowAddNote: false,
            UserId: null,
            NoteList: [],
            isLoading: false,
            noteId: 0,
            noteDataById: []
        }
    },
    watch: {
        NoteData: function (val) {
            debugger;
            var vm = this;
            vm.NoteList = val;
            if (vm.NoteList.length > 0) {
                vm.NoRecords = false;
            }
            else {
                vm.NoteList = [];
                vm.NoRecords = true;
            }
        }
    },
    created() {
        debugger
        setTimeout(() => {
        var vm = this;
        vm.UserId = vm.GetUserInfo.ID;
        vm.ListNoteData = this.ListNoteData;
        if (!vm.isBlank(vm.NoteData)) {
            vm.NoteList = vm.NoteData;
            vm.NoRecords = false;
        }
        else {
            vm.NoRecords = true;
            vm.NoteList = [];
        }
    }, 500);
    },

    methods: {
        EditNote: function (item) {
            debugger;
            var vm = this;
            vm.$parent.noteId = item.NOTE_ID;
            vm.$parent.ticketId = item.TICKET_ID;
            vm.$parent.note = item.NOTE;
            vm.$parent.isShowAddNote = true;
        },
        DownloadAttachment(item) {
            //debugger;
            var vm = this;
            vm.isLoading = true;
            var param = `filePath=${item.ATTACHMENT_PATH}&fileName=${item.ATTACHMENT_NAME}`;
            DataServices.DownloadFile(param).then(function (response) {
                ////debugger;
                vm.isLoading = false;
                const byteData = response.data;
                const fileURL = `data:${((JSON.parse(item.ATTACHMENT_DESC)).ContentType)};base64,${byteData}`;
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;
                fileLink.setAttribute('download', item.ATTACHMENT_NAME);
                document.body.appendChild(fileLink);
                fileLink.click();
            });
            vm.isLoading = false;
        }
    }
}
</script>