<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="clearfix"></div>
        <div class="row custom-box-width" id="OuterDivForVueCodeAttachment">
            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 default-tag-vissible" v-for="(item, index) in AttachmentList"
                :key="index"> 
                <div id="div_ShowGpxImage" class="col-12 p-0 d-flex flex-wrap"></div>
                <div class="box border positionrelative float-left w-100">
                    <div class="default-tags">
                        <div class="defaultpic_control">
                            <a v-if="RequestFrom == 'FSM' && item.GEOFILE_PATH != null && item.GEOFILE_PATH != ''"
                                href="javascript:;" class="text-primary round-icons mr-2 dev_ShowGPXPopup"
                                :title="$t('ViewOnMap')" :value="item.ticket_attachment_id" :data-fileName="item.attachment_name"
                                :data-filePath="item.C[0].ATTACHMENTFULLPATH" :data-fileType="item.attachment_desc"
                                :data-GpxFile="item.GEOFILE_PATH" data-placement="left">
                                <em class="fa fa-globe"></em>
                            </a>
                            <a v-if="ListViewAttachment == 1" href="javascript:;" class="text-primary round-icons mr-2" v-on:click="showImg(item.C[0].ATTACHMENTFULLPATH, item.attachment_name)"
                                :title="$t('View')" :value="item.ticket_attachment_id" :fileName="item.attachment_name"
                                :filePath="item.ticketFilePath" :fileType="item.contentType" data-placement="left">
                                <em class="fa fa-eye"></em><br />
                            </a>
                            <a href="javascript:;" class="text-primary round-icons mr-2 downloadfile"
                                v-on:click="DownloadAttachment(item)" :title="$t('DownloadFile')" data-placement="left">
                                <em class="fa fa-download"></em><br />
                            </a>
                            <a v-if="item.CREATED_BY == UserId" href="javascript:;"
                                class="text-primary round-icons mr-2 attachmentdraftdelete"
                                v-on:click="deleteAttachment(item)" :title="$t('ClickHereDeleteAttachment')"
                                :value="item.ticket_attachment_id" :fileName="item.attachment_name" :filePath="item.attachment_path"
                                :fileType="item.attachment_desc" data-placement="left">
                                <em class="fa fa-trash text-danger"></em><br />
                            </a>
                        </div>
                    </div>

                    <div class="img-ico text-center p-4 ticket-attachments">                      
                        <em v-if="!setImageIcon(item)" class="fa fa-file-video-o"></em>
                        <em v-else-if="setImageIcon(item) == 'word-ico.png'" class="fa fa-file-word-o"></em>
                        <em v-else-if="setImageIcon(item) == 'excel-ico.png'" class="fa fa-file-excel-o"></em>
                        <em v-else-if="setImageIcon(item) == 'pdf-ico.png'" class="fa fa-file-pdf-o"></em>
                        <img v-else-if="setImageIcon(item) == 'png-ico.png'" :src="item.C[0].ATTACHMENTFULLPATH" alt=""
                            class="img-fluid" style="width: auto; height: 102px; min-height: 102px;" />
                        <em v-else-if="setImageIcon(item) == 'audio-ico.png'" class="fa fa-file-audio-o"></em>
                        <em v-else-if="setImageIcon(item) == 'file-ico.png'" class="fa fa-file-text-o"></em>      
                        <template>
                            <img v-if="setImageIcon(item) != '/Content/Responsive/images/png-ico.png'"
                                :src="setImageIcon(item)" alt="" class="img-fluid"
                                style="width: auto; height: 102px; min-height: 102px;" />
                            <img v-else :src="item.C[0].ATTACHMENTFULLPATH" alt="" class="img-fluid"
                                style="width: auto; height: 102px; min-height: 102px;" />
                        </template>                
                    </div>
                    <hr class="my-1" />
                    <div class="col-12 p-0 mx-0 align-items-end float-left">
                        <div class="col-12 textelipsis px-2">{{ item.attachment_name }}</div>
                        <div class="col-md-12 px-2 py-1 bg-light mt-1 float-left">
                            <small class="float-left textelipsis col-6 p-0"><span>{{ item.PostedBy }}</span></small>
                            <small class="float-right col-6 text-right p-0">{{ item.CREATED_AT }}</small>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-sm-12 col-md-12 col-lg-12 mt-2 float-left p-0" v-if="NoRecords">
                <span class="text-danger text-center no-record" style="display:block;">{{ $t('NoRecordfound') }}</span>
            </div>
        </div>

        <!-- <vue-image-lightbox-carousel v-bind:v-if="visible" v-bind::images="GalleryImages" v-bind:index="index"
            @@hide="handleHide"></vue-image-lightbox-carousel> -->
    </section>
</template>

<script>
import DataServices from "@/services/DataService";
//import VueEasyLightbox from 'vue-image-lightbox-carousel';
export default {
    components:{
        //VueEasyLightbox
    },
    props: {
        AttachmentData: {
            type: Array,
            required: true
        },
        galleryImages:{
            type:Array,
            required:false
        },
        viewMode:{
            type:String,
            required:false
        },
        ListViewAttachment:{
            type:Number,
            required:false
        }
    },
    data() {
        return {
            AttachmentList: [],
            ListViewAttachment : 0,
            GalleryImages:[],
            NoRecords: false,
            visible:false,
            RequestFrom: null,
            UserId: null,
            isLoading: false,
            index:0
            
        }
    },
    watch: {
        AttachmentData: function(val){
            var vm =this;
            vm.AttachmentList = val;
            if (vm.AttachmentList.length > 0) 
            {
                vm.NoRecords = false;
            }
            else
            {
                vm.AttachmentList = [];
                vm.NoRecords = true;
            }
        }
    },
    created() {
        debugger;
        var vm = this;
        vm.ListViewAttachment = this.ListViewAttachment;
        setTimeout(() => {
        vm.UserId = vm.GetUserInfo.ID;
        vm.RequestFrom = vm.AttachmentData.RequestFrom;
        vm.GalleryImages = vm.galleryImages;
        if (vm.AttachmentData.length > 0) {
            vm.AttachmentList = vm.AttachmentData;
            vm.NoRecords = false;
        }
        else {
            vm.AttachmentList = [];
            vm.NoRecords = true;
        }
        }, 1000);
    },
    methods: {
        handleHide: function () {
            this.visible = false
        },
        showImg: function (attachmentPath, attachmentName) {
            //debugger;
            var vm = this;
            vm.$parent.galleryImages = [];
            vm.$emit('open-image', attachmentPath, attachmentName);
            vm.$parent.visible = true;
            //vm.$parent.index = index; 
        },
        setImageIcon(item) {
            var vm = this;
            var imageIcon = "/Content/Responsive/images/";
            var extension = vm.getExtension(item.attachment_name);
            switch (extension.toLowerCase()) {
                case "doc":
                case "docx":
                    imageIcon = "word-ico.png";
                    break;
                case "xlsx":
                case "xls":
                    imageIcon = "excel-ico.png";
                    break;
                case "pdf":
                    imageIcon = "pdf-ico.png";
                    break;
                case "jpg":
                case "jpeg":
                case "png":
                case "gif":
                case "bmp":
                    imageIcon = "png-ico.png";
                    break;
                case "webm":
                case "mov":
                case "mpv":
                case "mp4":
                case "avi":
                    imageIcon = ""; //"video-ico.png";
                    break;
                case "mp3":
                case "wav":
                    imageIcon = "audio-ico.png";
                    break;
                default:
                    imageIcon = "file-ico.png";
                    break;
            }
            return imageIcon;

        },
        getExtension(fileName) {
            ////debugger;
            let baseName = fileName.split(/[\\/]/).pop(),
                pos = baseName.lastIndexOf(".");
            if (baseName === "" || pos < 1)
                return "";
            return baseName.slice(pos + 1);
        },
        deleteAttachment: function (item) {
            debugger;
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, vm.$t('Remove'), function () {
                var param = `attachmentid=${item.ticket_attachment_id}`;
            DataServices.DeleteIncidentAttachment(param).then(function (response) {
                if (response.data != null && response.data != "") {
                    if (response.data.status == "Success") {
                        if (response.data.result.MSG == "SUCCESS") {
                            if(vm.viewMode == "AttachmentInDetail")
                            {
                                document.getElementById('tab-attachment').click();
                                // vm.$parent.GetAttachmentData();
                            }
                            else
                            {
                                vm.$parent.FetchAttachmentData();
                            }
                            
                            if (vm.ticket_attachment_id > 0) {
                                vm.ShowAlert(vm.$t('Attachment has been successfully deleted'), "success", true, vm.$t('Alert'));
                                //this.getIncidentAttachments(item.LINKED_ID);

                            } else {
                                vm.ShowAlert(vm.$t('Attachment has been successfully deleted'), "success", true, vm.$t('Alert'));
                                vm.$emit('close');
                            }
                            // checkVue.noStatus=true;
                        } else {
                            vm.ShowAlert(vm.$t('ErrorWhileSavingRecords', [vm.$t('failure')]), "success", true, vm.$t('Alert'));
                        }
                    } else {
                        vm.ShowAlert(vm.$t('ErrorWhileSavingRecords', [vm.$t('failure')]), "success", true, vm.$t('Alert'));
                    }
                } else {
                    vm.ShowAlert(vm.$t('ErrorWhileSavingRecords', [vm.$t('failure')]), "success", true, vm.$t('Alert'));
                }
            });
            });            
        },
        DownloadAttachment(item) {
            debugger;
            var vm = this;
            vm.isLoading = true;
            var param = `filePath=${item.attachment_path}&fileName=${item.attachment_name}`;
            DataServices.DownloadFile(param).then(function (response) {
               // //debugger;
                vm.isLoading = false;
                const byteData = response.data;
                const fileURL = `data:${((JSON.parse(item.attachment_desc)).ContentType)};base64,${byteData}`;
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;
                fileLink.setAttribute('download', item.attachment_name);
                document.body.appendChild(fileLink);
                fileLink.click();
            });
            vm.isLoading = false;
        }
    }
}
</script>