<template>
    <section>
        <div class="row card-view no-bg custom-box-width" id="divclaimattch" >
            
                <div v-for="(item,index) in ClaimList" v-bind:key="index" class="col-md-12 col-lg-6 col-xl-3 mb-3" :id="'trclaimList_'+item.claimId">
                    <div class="card-box blue-bg-light">
                        <div class="card-heading">
                            <span class="user-img-con mr-2">
                                    <!-- <img v-if="Avatar" src="@CloudImageHelper.GetUserProfileImage("Upload/UserProfileImage/", loggedUserInfo.Avatar)" :alt="$t('Image')" />                                -->
                                    <img src='/Content/images/DefaultImages/avatar-icon.jpg' :alt="$t('Image')" border="0" />                               
                            </span>
                            {{!item.createdByDepartmentName ? '--' : item.createdByDepartmentName}}
                        </div>
                        <div class="card-detail-main1">
                            <div class="card-detail"><em class="fa fa-user" aria-hidden="true"></em><span>{{item.createdByName}}</span></div>
                            <p>{{$t('ClaimValue')}}: <span v-html="item.claimValue"></span></p>
                            <hr class="my-2" />
                            
                            <span v-if="item.approvedClaimAmount >0" class="ticket-priority greentxt">{{$t('ApprovedClaimAmount') +' ' +item.approvedClaimAmount}}</span>                            
                            <div class="commentnoteslist" style="max-height:265px;"><div class="custom-scrollbar-js" v-html="item.claimNotes"></div></div>              
                         
                            <div v-if="item.createdBy == UserId && item.statusCode == 'TICKET_CLAIM_DRAFT'">
                                <div v-if="item.claimId > 0 && item.attachmentsCount > 0" :class="'ifattachmentviss-cont d-none '+item.claimId">
                                    <div :data-ClaimId="item.claimId" class="attachments">
                                    </div>
                                </div>
                            </div>
                            <div v-if="item.statusId==1001">
                                <div v-i="item.claimId > 0 && item.attachmentsCount > 0" :class="'ifattachmentviss-cont d-none '+item.claimId">
                                    <div :data-ClaimId="item.claimId" class="attachments">
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div v-if="item.arComments">
                            
                            <div v-if="item.approvedClaimAmount > 0">
                           
                                <hr class="my-2">
                                <a class="fontlinkblue showCliamARComment approved-reject-cmnt" v-on:click="isShowingApprove = !isShowingApprove" href="javascript:;" :data-claimId="item.claimId" :title="$t('ApprovedClaimComment')">
                                    {{$t('ApprovedClaimComment')}}
                                </a>
                            </div>
                            <div v-else>
                                <hr class="my-2">
                                <a class="fontlinkblue showCliamARComment approved-reject-cmnt" v-on:click="isShowingApprove = !isShowingApprove"  href="javascript:;" :data-claimId="item.claimId" :title="$t('RejectClaimComment')">
                                    {{$t('RejectClaimComment')}}
                                </a>
                            </div>
                            <div style="clear:both;"></div>
                            <div class="commentlist" v-if="isShowingApprove" style="max-height: 260px; margin-bottom: 50px;"><div class="custom-scrollbar-js" v-html="item.arComments"></div></div>
                            <!-- <div class="commentlist"  style="max-height: 260px; margin-bottom: 50px;"><div class="custom-scrollbar-js">{{item.arComments}}</div></div> -->
                        </div>
                        <div class="bottom-action" style="padding-top: 8px; padding-bottom: 8px;">
                            <div class="card-date">
                                <em class="fa fa-calendar-o" aria-hidden="true"></em>
                                <span> {{!item.createdAt ? '--' : $options.filters.formatDateTime(item.createdAt)}}</span>
                            </div>
                            <div class="card-action">
                                    <a v-if="item.claimId > 0 && item.attachmentsCount > 0 && item.statusId == 1001" href="javascript:;"  @click="DownloadAttachment(item)" :data-ClaimId="item.claimId" class="anchrAttachmentsclaim round-icon-small btn-info-light"><em class="fa fa-paperclip text-white p-2"></em></a>
                                    <a checkprivilege="Yes" v-if="(isTicketHod==1 || IsDefault) && item.statusId == 1001" :title="$t('Approve')" @click="ApproveTicketClaim(item,'A')" class="approveClaim round-icon-small btn-success-light" :data-ClaimId="item.claimId" :data-claimAmount="item.claimValue" :data-createdby="item.createdBy" :data-createdbyName="item.createdByName" style="cursor:pointer;"><em class="fa fa-thumbs-up text-white"></em></a>
                                    <a checkprivilege="Yes" v-if="(isTicketHod==1 || IsDefault) && item.statusId == 1001" :title="$t('Reject')" @click="ApproveTicketClaim(item,'R')" class="rejectClaim round-icon-small btn-danger-light" :data-ClaimId="item.claimId" :data-claimAmount="item.claimValue" :data-createdby="item.createdBy" :data-createdbyName="item.createdByName" style="cursor:pointer;"><em class="fa fa-thumbs-down text-white"></em></a>
                                    <a v-if="item.claimId > 0 && item.attachmentsCount > 0 && item.createdBy == UserId && item.statusCode=='TICKET_CLAIM_DRAFT'" href="javascript:;" :data-ClaimId="item.claimId" class="anchrAttachmentsclaim round-icon-small btn-info-light"><em class="fa fa-paperclip text-white p-2"></em></a>
                                    <a v-if="item.createdBy == UserId && item.statusCode=='TICKET_CLAIM_DRAFT'" @click="EditTicketClaim(item)" href="javascript:;" :title="$t('Edit')" class="editTicketClaim  round-icon-small btn-success-light" :data-ClaimId="item.claimId" :data-claimAmount="item.claimValue" :data-createdby="item.createdBy" :data-createdbyName="item.createdByName" style="cursor:pointer;"><em class="fa fa-pencil"></em></a>
                                    <a v-if="item.createdBy == UserId && item.statusCode=='TICKET_CLAIM_DRAFT'" @click="DeleteTicketClaim(item)" href="javascript:;" :title="$t('Delete')" class="clsdeleteticketclaim  round-icon-small btn-danger-light" :data-ClaimId="item.claimId" :value="item.ticketId"  style="cursor:pointer;"><em class="fa fa-trash-o"></em></a>
                            </div>
                        </div>
                    </div>
                </div>
          
            <div class="col-sm-12 col-md-12 col-lg-12 mt-2" v-if="NoRecords">
                <span class="text-danger text-center no-record" style="display:block;">{{$t('NoRecordfound')}}</span>
            </div>
        </div>
        <AddClaimData v-if="isShowAddClaim" :id="ticketId" :claimId="claimId" modulename="Ticketing" submodulecode="Ticketing" v-bind:callbackfunction="GetClaimData" :HeaderName="$t('AddClaim')"></AddClaimData>
        <transition name="modal">
         <div class="my-popups">
           <loader :is-visible="isLoading"></loader>
              <div class="modal d-block mail-box-pop dialog" v-if="approveTicketClaim">
                <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title" v-if="isApproveTitle">{{ $t('Approve') +' '+ $t('Ticket') +' '+ $t('Claim')}}</h4>
                        <h4 class="modal-title" v-if="isRejectTitle">{{ $t('Reject') +' '+ $t('Ticket') +' '+ $t('Claim')}}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="approveBtn" :schema="approveFormSchema" v-on:OnSubmit="SaveApproveTicketClaim">                            
                             <template v-slot:tgslot-Description="{ data }">
                                <ckeditor :editor="editor" @ready="onReady" v-model="data.fieldInfo.value"></ckeditor>
                                <span name="Description" class="invalid-feedback d-block">{{ data.error }}</span>
                                <!-- {{ data.fieldInfo }} -->
                            </template>
                        </dynamic-form>
                    </div>
                </div>
                </div>
               </div>
          </div>
        </transition>
    </section>
</template>

<script>
import AddClaimData from "@/components/Modal/AddClaimModal.vue";
import DataServices from "@/services/DataService";
//import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
export default{
    components:{
        AddClaimData,
    },
    props:{
        ClaimData:{
            type:Array,
            required:true
        }
    },
    data(){
        return{
            editor: this.CLASSIC_EDITOR,
            content: "<h1>Some initial content</h1>",
            UserId:null,
            ClaimList:[],
            NoRecords:false,
            isLoading: false,
            ClaimAmount:'',
            btnType:'',
            confirmMsg:'',
            successMsg:'',
            Description:null,
            approveTicketClaim:false,
            isShowingApprove:true,
            isApproveTitle:false,
            isTicketHod:0,
            IsDefault:false,
            isShowAddClaim: false,
            ApproveClaimTicketModel :
            {
                ClaimNote : '',
                ClaimValue : '',
                TicketId : 0,
                ClaimId : 0,
                Type : ''
            },
            approveFormSchema: [
                {
                    layoutType: "single",
                    Data: [
                            {
                                astype: "TextField", 
                                label: this.$t('Amount (USD)'),                          
                                name: "ClaimAmount",   
                                value:"",                             
                                placeholder: "",
                                validationRules: { "required": true, max: 200 },  
                                visibility: false
                            },
                            {
                            astype: "SlotField",
                            label: this.$t('Claim Note'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                              },
                              settings: {
                                isInSlot: true
                            },
                            visibility: true,
                            validationRules: { "required": true, max: 200 },  
                           },                                       
                    ]
                }
            ],
            approveBtn: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-thumbs-up pr-2'></i>Approve",
                    isVisible: false
                }
                , {
                    type: "submit",
                    class: "btn btn-danger save",
                    text: "<i class='fa fa-thumbs-down pr-2'></i>Reject",
                    isVisible: false
                }
            ]
        }
    },
    created(){
        var vm=this;
        vm.UserId=vm.GetUserInfo.ID;
        vm.IsDefault=vm.GetUserInfo.IsDefault;
        vm.companyId = this.GetUserInfo.CompanyId;
        if(vm.ClaimData.isTicketHod != undefined){
            vm.isTicketHod=vm.ClaimData.isTicketHod;
        }
        if(vm.ClaimData.claim.length > 0){
            vm.ClaimList=vm.ClaimData.claim;
            vm.NoRecords=false;
        }
        else{
            vm.ClaimList=[];
            vm.NoRecords=true;
        }
    },
    methods:{
        EditTicketClaim: function (item) {
            var vm = this;
            vm.ticketId = item.ticketId;
            vm.claimId = item.claimId;
            vm.isShowAddClaim = true;
        },
        DownloadAttachment(item){
            var vm=this;
            vm.isLoading=true;
            var param=`filePath=${item.attachmentPath}&fileName=${item.attachmentName}`;
            DataServices.DownloadFile(param).then(function (response) {
                vm.isLoading=false;
                const byteData=response.data;
                const fileURL = `data:${((JSON.parse(item.attachmentDesc)).ContentType)};base64,${byteData}`;
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;
                fileLink.setAttribute('download', item.ticketFileName);
                document.body.appendChild(fileLink);
                fileLink.click();
            });     
        },
        DeleteTicketClaim: function (item){
            debugger;
            var vm=this;
            vm.isLoading=true;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Claim")), function () {
                    var postJSON = {
                        ids: item.claimId,
                        companyId: vm.companyId,
                        userId: vm.UserId,
                        moduleName: "FSM",
                        refCode: "TALYGEN_TICKET_CLAIM"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        var box = "";
                        vm.isLoading = false;
                        if (response.data[0].Status == "Success") {
                            document.getElementById('tab-cliam').click();
                            // vm.$parent.GetClaimData();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Claim')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Claim')]), "failure", true, vm.$t('Alert'));
                        }
                        vm.$emit('close');
                    });
                });    
        },
        ApproveTicketClaim: function (item,type){
            var vm=this;
            vm.btnType = type;
            vm.ticketId = item.ticketId;
            vm.claimId = item.claimId; 
           if(type =='A')
           {
                vm.confirmMsg = vm.$t("ClaimAcceptConfirmMessage");
                vm.successMsg = vm.$t("TicketClaimAcceptMessage");
                vm.approveBtn[0].isVisible = true;
                vm.approveBtn[1].isVisible = false;
                vm.approveFormSchema[0].Data[0].visibility=true;
                vm.isApproveTitle = true;
                vm.isRejectTitle = false;
           }
           else
           {
                vm.confirmMsg = vm.$t("ClaimRejectConfirmMessage");
                vm.successMsg = vm.$t("TicketClaimRejectMessage");
                vm.approveBtn[0].isVisible = false;
                vm.approveBtn[1].isVisible = true;
                vm.approveFormSchema[0].Data[0].visibility=false;
                vm.isApproveTitle = false;
                vm.isRejectTitle = true;
           }
            vm.confirmR(vm.confirmMsg,true, vm.$t('Confirm'),function(result) { 
                vm.approveTicketClaim = true;
                if(item.claimValue > 0)
                {
                    (vm.approveFormSchema)[0].Data[0].value = item.claimValue;
                }
            });
        },
        SaveApproveTicketClaim: function (formfields, val) {
            var vm = this;  
            vm.ApproveClaimTicketModel.ClaimId = vm.claimId;
            vm.ApproveClaimTicketModel.ClaimValue = vm._data.approveFormSchema[0].Data[0].value;
            vm.ApproveClaimTicketModel.ClaimNote = vm._data.approveFormSchema[0].Data[1].value;
            vm.ApproveClaimTicketModel.TicketId =  vm.ticketId;
            vm.ApproveClaimTicketModel.Type =  vm.btnType;
            var formData = JSON.stringify(vm.ApproveClaimTicketModel);
            vm.confirmR(vm.confirmMsg,true, vm.$t('Confirm'),function(result) { 
                DataServices.ApproveRejectClaim(formData).then((response) => 
                {
                       try 
                        {
                            if (response.data != null && response.data != "" && response.data.status == "Success") {       
                                vm.ShowAlert(vm.successMsg, "success", true, vm.$t("Alert"));  
                                vm.Close();    
                                // document.getElementById('tab-cliam').click();                              
                                // vm.callbackfunction();
                            }
                            
                       } 
                        catch (exinner) 
                        {                                    
                            return HandleErrorOccured(exinner, 'failure');
                       }
                });    
            });
         },
         Close: function () {
                var vueObj = this;
                (vueObj.approveFormSchema)[0].Data[0].value = null;
                (vueObj.approveFormSchema)[0].Data[1].value = null;
                vueObj.approveTicketClaim = false;
                // vueObj.$parent.GetClaimData();
                document.getElementById('tab-cliam').click();
         },    
    }
}
</script>