<loader :is-visible="isLoading"></loader>
<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">{{ $t('Update') }}</h5>
                            <button v-on:click="$emit('close')" href="javascript:;" class="close">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <span class="user-guide">
                                <a class="ancuserguide" data-toggle="collapse" href="#collapseReply" role="button"
                                    aria-expanded="false" aria-controls="collapseExample">{{ $t('UserGuide') }}</a>
                                <div id="collapseReply" class="divancuserguide collapse">
                                    <div id="scrollbarmergeuserguide" class="custom-scrollbar-js">
                                        <div class="con" v-html="$t('UgTicketingViewUpdate')"></div>
                                    </div>
                                </div>
                            </span>
                            <dynamic-form v-if="DataLoaded" lang="en" :buttons="buttons" :schema="FormSchema"
                                v-on:OnSubmit="onSubmit"></dynamic-form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from "@/services/DataService";
export default {
    props: {
        TicketId: {
            type: String,
            required: true
        },
        modulename: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
        callbackfunction: {
            type: Function
        }
    },
    data() {
        return {
            statusList: [],
            CCUserList: [],
            CategoryList: [],
            priorityList: [],
            productList: [],
            DataLoaded: 0,
            ccOption: [],
            isLoading: false,
            isProductChanged: 0,
            isETAChanged: 0,
            isStatusChanged: 0,
            isCategoryChanged: 0,
            isPriorityChanged: 0,

            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeDropdown
                            },
                            validationRules: "",
                        },
                        {
                            astype: "DateField",
                            label: this.$t('Expected Resolution (M/D/YYYY)'),
                            name: "ExpectedResolution",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false },
                        },
                        {
                            astype: "MultiSelectField",
                            mode: "tag",
                            label: this.$t('CC Users'),
                            name: "CCUsers",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('CC Users'),
                            validationRules: { "required": false },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Ticket Category'),
                            name: "TicketCategory",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeDropdown
                            },
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Product'),
                            name: "Product",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeDropdown
                            },
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Priority'),
                            name: "Priority",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeDropdown
                            },
                            validationRules: "",
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Update"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.onCancel
                }
            ]
        }
    },
    async created() {
        var vm = this;
        await vm.GetStatusList();
        await vm.GetCCUserList();
        await vm.GetCategoryForTicket();
        await vm.GetPrioritybyCompanyId();
        await vm.GetProductsList();
        vm.DataLoaded = 1;

        vm.FetchTicketData();

    },
    methods: {
        FetchTicketData: async function () {
            var vm = this;
            vm.isLoading = true;
            var url = `ticketId=${vm.TicketId}`;
            await DataService.GetTicketById(url).then(function (response) {
                vm.isLoading = false;
                //debugger;
                if (response.data.ticket != null) {
                    vm._data.FormSchema[0].Data[0].value = response.data.ticket.statusId;
                    vm._data.FormSchema[0].Data[1].value = response.data.ticket.expectedResolutionDate;
                    if (!vm.isBlank(response.data.ticket.ticketCategoryId)) {
                        vm._data.FormSchema[0].Data[3].value = response.data.ticket.ticketCategoryId;
                    }
                    if (!vm.isBlank(response.data.ticket.productId)) {
                        vm._data.FormSchema[0].Data[4].value = response.data.ticket.productId;
                    }

                    vm._data.FormSchema[0].Data[5].value = response.data.ticket.priorityId;
                    var ccuser = response.data.ticket.ccUser;
                    if (!vm.isBlank(ccuser)) {
                        var bindCCUser = [];
                        vm.ccOption = vm.CCUserList.filter(f => ccuser.indexOf(f.value) > -1);
                        vm.ccOption.forEach((item, index) => {
                            bindCCUser.push({
                                value: item.value,
                                label: item.name
                            })
                        });
                        vm._data.FormSchema[0].Data[2].value = bindCCUser;
                    }
                }
            })
        },
        GetStatusList: async function () {
            var vm = this;
            var params = `statusType=${"Ticketing"}&&hasglobal=${false}`;
            await DataService.GetStatusList(params).then(async (response) => {
                if (response.data.length > 0) {
                    vm.statusList = [];
                    vm.statusList = response.data;
                    (vm.FormSchema)[0].Data[0].config.options = []
                    vm.statusList.forEach((item, index) => {
                        (vm.FormSchema)[0].Data[0].config.options.push({
                            value: item.STATUS_ID,
                            name: item.STATUS
                        })
                    })
                }
            });
        },
        GetCCUserList: async function () {
            var vm = this;
            var params = `id=&moduleName=Ticketing&type=CC_USER_LIST&search=&refId=`;
            // var params = `reqFrom=${'all'}`;
            await DataService.GetDDLData(params).then(async (response) => {
                //debugger;
                if (response.data.DATA.length > 0) {
                    var json = response.data.DATA;
                    vm.CCUserList = response.data.DATA;
                    if (json != null) {
                        response.data.DATA.forEach((item, index) => {
                            (vm.FormSchema)[0].Data[2].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        GetCategoryForTicket: async function () {
            var vm = this;
            var params = `categoryTypeId=${'TICKET_CATEGORY'}&is_Global=${false}&showActiveStatus=${true}`;
            await DataService.GetDDLCategoryForTicket(params).then(async (response) => {
                if (response.data.length > 0) {
                    vm.CategoryList = [];
                    vm.CategoryList = response.data;
                    (vm.FormSchema)[0].Data[3].config.options = [];
                    vm.CategoryList.forEach((item, index) => {
                        (vm.FormSchema)[0].Data[3].config.options.push({
                            value: item.value,
                            name: item.name
                        })
                    })
                }

            });
        },
        GetProductsList: async function () {
            var vm = this;
            await DataService.GetProductsList().then(async (response) => {
                if (response.data.length > 0) {
                    vm.productList = response.data;
                    vm.productList.forEach((item, index) => {
                        (vm.FormSchema)[0].Data[4].config.options.push({
                            value: item.value,
                            name: item.name
                        })
                    })
                }
            });
        },
        GetPrioritybyCompanyId: async function () {
            var vm = this;
            await DataService.GetPrioritybyCompanyId().then(async (response) => {
                if (response.data.length > 0) {
                    vm.priorityList = response.data;
                    vm.priorityList.forEach((item, index) => {
                        (vm.FormSchema)[0].Data[5].config.options.push({
                            value: item.prioritY_ID,
                            name: item.prioritY_NAME
                        })
                    })
                }
            });
        },
        onChangeDropdown: async function (e) {
            ////debugger;
            var vm = this;
            var dropName = e.target.name;
            if (dropName == "Product") {
                vm.isProductChanged = 1;
            }
            else if (dropName == "Priority") {
                vm.isPriorityChanged = 1;
            }
            else if (dropName == "TicketCategory") {
                vm.isCategoryChanged = 1;
            }
            else if (dropName == "Status") {
                vm.isStatusChanged = 1;
            }
        },
        onSubmit: async function (formfields, val) {
            var vm = this;
            //debugger;
            var CCUsers = '';
            var i = 0;
            for (i = 0; i < vm._data.FormSchema[0].Data[2].value.length; i++) {
                if (i == 0) {
                    CCUsers = vm._data.FormSchema[0].Data[2].value[i].value;
                } else {
                    CCUsers = CCUsers + "," + vm._data.FormSchema[0].Data[2].value[i].value;
                }
            }
            var postJSON = {
                ticketId: vm.TicketId,
                statusId: vm._data.FormSchema[0].Data[0].value,
                isSuptTckt: vm.user_id,
                productId: vm.isBlank(vm._data.FormSchema[0].Data[4].value) !== true ? vm._data.FormSchema[0].Data[4].value : null,
                ccOption: vm.isBlank(CCUsers) !== true ? CCUsers : null,
                ticketCategory: vm.isBlank(vm._data.FormSchema[0].Data[3].value !== true) ? vm._data.FormSchema[0].Data[3].value : null,
                priorityId: vm.isBlank(vm._data.FormSchema[0].Data[5].value !== true) ? vm._data.FormSchema[0].Data[5].value : null,
                expectedResolutionDate: vm.isBlank(vm.$options.filters.formatDate(vm.FormSchema[0].Data[1].value)) != true ? vm.$options.filters.formatDate(vm.FormSchema[0].Data[1].value) : null,
                isProductChanged: vm.isBlank(vm.isProductChanged) !== true ? vm.isProductChanged : 0,
                isETAChanged: vm.isBlank(vm.$options.filters.formatDate(vm.FormSchema[0].Data[1].value)) !== true ? 1 : 0,
                isStatusChanged: vm.isBlank(vm.isStatusChanged) !== true ? vm.isStatusChanged : 0,
                isCategoryChanged: vm.isBlank(vm.isCategoryChanged) !== true ? vm.isCategoryChanged : 0,
                isPriorityChanged: vm.isBlank(vm.isPriorityChanged) !== true ? vm.isPriorityChanged : 0,
                ticketOpenDate: vm.isBlank(vm.$options.filters.formatDateTime(vm.FormSchema[0].Data[1].value)) !== true ? vm.$options.filters.formatDateTime(vm.FormSchema[0].Data[1].value) : null
            };
            var objectPostString = JSON.stringify(postJSON);
            DataService.TicketUpdate(objectPostString).then((response) => {
                ////debugger;
                try {
                    ////debugger;
                    if (response.data != null && response.data != "" && response.data.status == "Success") {
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Ticket")]), "success", true, vm.$t("Alert"));
                        $("input[type=text]").val('');
                        vm.Close();

                    }
                    else {
                        vm.FileSrc = '';
                        vm.Close();
                        $("input[type=text]").val('');

                    }
                } catch (exinner) {
                    return HandleErrorOccured(exinner, 'Ticket');
                }
            });
        },
        Close: function () {
            var vm = this;
            vm._data.FormSchema[0].Data[0].value = null;
            vm._data.FormSchema[0].Data[1].value = null;
            vm._data.FormSchema[0].Data[2].value = null;
            vm._data.FormSchema[0].Data[3].value = null;
            vm._data.FormSchema[0].Data[4].value = null;
            vm._data.FormSchema[0].Data[5].value = null;
            vm.$parent.isShowTicketUpdate = false;
            vm.callbackfunction();
        }
    }
}
</script>