<template>
    <section id="main-content" class="main-content">
        <!-- start page heading -->
        <section class="page-action" v-if="ticketType == 1">
            <div class="container-fluid">
                <div class="row">
                    <div class="col-md-12 padding-t_8">
                        <div class="theme-primary partition-full">
                            <span class="p-name text-white">{{ TicketNumber }}</span>
                            <span class="p-actions float-md-right float-left mb-wid-100">
                                <h5 class="Response-t px-3">Response Time:<br><span>00:00:00</span></h5>
                                <h5 class="Resolve-t px-3">Resolve Time:<br><span>00:00:00</span></h5>
                                <a href="javascript:;" class="e-c-map p-action-btn text-white align-items-center d-flex"
                                    v-on:click="ShowHideMap">
                                    <i id="icnToggleMap" class="fa fa-angle-double-up pr-2"></i>
                                    <span id="spnToggleMap">{{ mapHeaderText }}</span></a>
                                <a href="/Ticketing/Index" title=""
                                    class="p-action-btn text-white d-md-inline-block mble_50" data-toggle-tooltip="tooltip"
                                    data-original-title="Back to list"><i class="fa fa-angle-double-left"></i><br>Back to
                                    list</a>
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <!-- end page heading -->
        <section class="main-content">
            <div class="container-fluid padding-t_8">
                <map-component class="row" :mapType="mapType" v-if="ticketType == 1 && isShowMap == true"
                    mapContainer="detailMapDiv" :Data="mapObj" :config="config"
                    :UtilityMapData="UtilityMapData"></map-component>
                <loader :is-visible="isLoading"></loader>
            </div>
            <div class="container-fluid padding-t_10">
                <div class="left-menu-tab " id="divTicketDataDetails">
                    <div class="col-md-12 p-0 ">
                        <div class="theme-primary partition"> <span class="p-name text-white" style="font-size:19px;">{{
                            $t('Ticketing') }}</span> </div>
                    </div>
                    <div class="w-100 float-left mt-2">
                        <div class="input-group font-weight-bold">
                            <div class="input-group-prepend w-100">
                                <select v-model="userstatus" name="ddlUserCurrentStatus"
                                    class="form-control d-inline-block float-right"
                                    style="max-width:350px; min-width:100px;" id="ddlUserCurrentStatus"
                                    v-on:change="UserCurrentStatus()">
                                    <option value="Busy">Busy</option>
                                    <option value="Offline">Offline</option>
                                    <option value="Online">Online</option>
                                </select>
                            </div>
                        </div>
                    </div>
                    <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab w-100 float-left" id="myTab"
                        role="tablist">
                        <li class="nav-item">
                            <a class="nav-link active left-menu" @click="LoadTab('activities')" data-toggle="tab"
                                id="tab-ticketdetails" href="#tab-ticket-content" role="tab" aria-controls="Activities"
                                aria-selected="false"><em class="fa fa-calendar" aria-hidden="true"></em>{{ $t('Activities')
                                }}</a>
                        </li>
                        <li class="nav-item" v-if="UserType != 'CL'">
                            <a class="nav-link left-menu" @click="LoadTab('notes')" data-toggle="tab"
                                href="#tab-notes-content" role="tab" aria-controls="Notes" aria-selected="false"
                                id="tab-notes"><em class="fa fa-file-text" aria-hidden="true"></em> {{ $t('Notes') }} <span
                                    id="spntotalticketnotes">
                                    {{ TicketData.noOfNotes > 0 ? '(' + TicketData.noOfNotes + ')' : '' }}</span> </a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link left-menu" @click="LoadTab('attachments')" data-toggle="tab"
                                href="#tab-attachment-content" role="tab" aria-controls="Attachments" id="tab-attachment"
                                aria-selected="false">
                                <em class="fa fa-file" aria-hidden="true"></em> {{ $t('Attachments') }}
                                <span id="spntotalticketattachment">{{ TicketData.attachmentsCount > 0 ?
                                    '(' + TicketData.attachmentsCount + ')' : '' }}</span>
                            </a>
                        </li>
                        <li class="nav-item" v-if="TicketData.isFieldServiceTicket">
                            <a class="nav-link left-menu" @click="LoadTab('relatedticket')" data-toggle="tab"
                                id="tab-relatedticket" href="#tab-relatedticket-content" role="tab"
                                aria-controls="RelatedTicket" aria-selected="false"><em class="fa fa-ticket"
                                    aria-hidden="true"></em> {{ $t('Related') + ' ' + $t('Ticket') }}</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link left-menu" @click="LoadTab('claims')" data-toggle="tab" id="tab-cliam"
                                href="#tab-cliam-content" role="tab" aria-controls="Claim" aria-selected="false"><em
                                    class="fa fa-dollar" aria-hidden="true"></em> {{ $t('Claim') }}<span
                                    id="spntotalticketclaim">
                                    {{ TicketData.noOfClaims > 0 ? '(' + TicketData.noOfClaims + ')' : '' }}</span> </a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link left-menu" @click="LoadTab('calls')" data-toggle="tab" id="tab-call"
                                href="#tab-call-content" role="tab" aria-controls="TrackHistory" aria-selected="false"><em
                                    class="fa fa-phone" aria-hidden="true"></em> {{ $t('Call') + ' ' + $t('Details') }} </a>
                        </li>
                        <li class="nav-item" v-if="TicketData.isFieldServiceTicket">
                            <a class="nav-link left-menu" @click="LoadTab('trackhistory')" data-toggle="tab"
                                id="tab-trackhistory" href="#tab-trackhistory-content" role="tab"
                                aria-controls="TrackHistory" aria-selected="false"><em class="fa fa-history"
                                    aria-hidden="true"></em> {{ $t('Track') + ' ' + $t('History') }}</a>
                        </li>
                        <li class="nav-item" v-if="TicketData.isFieldServiceTicket">
                            <a class="nav-link left-menu" @click="LoadTab('workorder')" data-toggle="tab" id="tab-workorder"
                                href="#tab-workorder-content" role="tab" aria-controls="WorkOrder" aria-selected="false"><em
                                    class="fa fa-circle-o-notch" aria-hidden="true"></em> {{ $t('WorkOrder') }}</a>
                        </li>

                    </ul>
                </div>
                <!-- end left panel -->
                <!--- start right panel content -->
                <div class="right-content">
                    <div class="col-md-12 p-0">
                        <div class="col-lg-12 p-0" id="tabscontentTicketDetails">
                            <div class="col-md-12 p-0">
                                <div class="theme-primary partition p-actions-expand">
                                    <span class="float-left p-name text-white">{{ TicketNumber }}</span>
                                    <span v-if="TicketData.isFieldServiceTicket" class="badge badge-success"
                                        style="padding: 7px 15px;margin-top: 8px;font-size: 16px;font-weight: normal;">FSM</span>
                                    <span v-if="!TicketData.isFieldServiceTicket" class="badge badge-primary"
                                        style="padding: 7px 15px;margin-top: 8px;font-size: 16px;font-weight: normal;">NORMAL</span>
                                    <span class="p-actions float-right" id="tabscontent">
                                        <a v-if="!TicketData.isFieldServiceTicket" href="javascript:;"
                                            :class="{ 'disabled': isDisabledHeaderButton, 'p-action-btn text-white edit': true }"
                                            @click="UpdateTicket()" :title="$t('Update')"><em class="fa fa-pencil"
                                                aria-hidden="true"></em><br />{{
                                                    $t('Update') }}</a>
                                        <a v-if="UserType == 'CA' || UserType == 'SA' || isTicketResolve == 'yes' || isTicketHOD == 'yes'"
                                            @click="isShowTransferTicket = true" href="javascript:;"
                                            :class="{ 'disabled': isDisabledHeaderButton, 'p-action-btn text-white transfer': true }"
                                            title="Assign / Transfer"><em class="fa fa-exchange"
                                                aria-hidden="true"></em><br />Assign / Transfer</a>
                                        <a href="javascript:;" v-if="!TicketData.isFieldServiceTicket"
                                            @click="isShowMergeTicket = true"
                                            :class="{ 'disabled': isDisabledHeaderButton, 'p-action-btn text-white merge': true }"
                                            :title="$t('Merge')"><em class="fa fa-random" aria-hidden="true"></em><br />{{
                                                $t('Merge') }}</a>
                                        <a href="javascript:;" v-on:click="openReplyModel()"
                                            :class="{ 'disabled': isDisabledHeaderButton, 'p-action-btn text-white reply': true }"
                                            :title="$t('Reply')"><em class="fa fa-reply" aria-hidden="true"></em><br />{{
                                                $t('Reply') }}</a>

                                        <a v-if="UserType != 'CL'" @click="isShowAddNote = true" href="javascript:;"
                                            :class="{ 'disabled': isDisabledHeaderButton, 'p-action-btn text-white addnotes': true }"
                                            role="tab" aria-controls="Notes" aria-selected="false"
                                            :title="$t('AddNotes')"><em class="fa fa-file-text"
                                                aria-hidden="true"></em><br />{{ $t('AddNotes') }}</a>

                                        <a v-if="UserType != 'CL' && TicketData.isFieldServiceTicket"
                                            v-on:click="openAttachmentModel()" href="javascript:;"
                                            :class="{ 'disabled': isDisabledHeaderButton, 'p-action-btn text-white addattachment': true }"
                                            role="tab" aria-controls="Attachment" aria-selected="false"
                                            title="Add Attachment"><em class="fa fa-file-text"
                                                aria-hidden="true"></em><br />{{ $t('Add') }} {{ $t('Attachment') }}</a>

                                        <a href="javascript:;" @click="isShowAddClaim = true"
                                            :class="{ 'disabled': isDisabledHeaderButton, 'p-action-btn text-white claim': true }"
                                            :title="$t('AddClaim')"><em class="fa fa-dollar"
                                                aria-hidden="true"></em><br />{{ $t('AddClaim') }}</a>

                                        <a href="javascript:;" v-on:click="openEscalatePopUp()"
                                            class="p-action-btn text-white esclatehs" :title="$t('Esclate')"><em
                                                class="fa fa-list" aria-hidden="true"></em><br />{{ $t('Esclate') }}</a>

                                        <!-- <a v-if="(TicketData.TicketSlaId > 0 && (GetUserInfo.ID != TicketData.CreatedBy || UserType=='CA' || UserType == 'SA' || isTicketResolve == 'yes' || isTicketHOD == 'yes')) " 
                                                 href="javascript:;" class="p-action-btn text-white esclatehs" :title="$t('Esclate')"><em class="fa fa-list pr-2" aria-hidden="true"></em><br />{{$t('Esclate')}}</a>
                                                  -->
                                        <a v-if="TicketData.followUpId && !TicketData.isFieldServiceTicket"
                                            @click="isshowFolloupList = true" href="javascript:;"
                                            :class="{ 'disabled': isDisabledHeaderButton, 'p-action-btn text-white manageFollowup': true }"
                                            role="tab" aria-controls="ManageFollowUp" aria-selected="false"
                                            :title="$t('ClickHereToEditFollowUp')"><em class="fa fa-flag ml-2 flag-color"
                                                aria-hidden="true"></em><br />{{ $t('FollowUp') }}</a>

                                        <a v-if="!TicketData.followUpId && !TicketData.isFieldServiceTicket"
                                            @click="isshowFolloupList = true" href="javascript:;"
                                            :class="{ 'disabled': isDisabledHeaderButton, 'p-action-btn text-white manageFollowup': true }"
                                            role="tab" aria-controls="ManageFollowUp" aria-selected="false"
                                            :title="$t('ClickHereToEditFollowUp')"><em class="fa fa-flag ml-2"
                                                aria-hidden="true"></em><br />{{ $t('FollowUp') }}</a>

                                        <a v-on:click="openStartPopUp()"
                                            v-if="TicketData.isFieldServiceTicket && AddEditText !== 'Edit' && ShowWorkOrderTimer === 'yes'"
                                            v-bind:class="{ 'disabled': isDisabledHeaderButton, 'd-none': ShowWorkOrderTimer == 'no' }"
                                            href="javascript:;" class="p-action-btn text-white" :title="$t('StartTimer')"
                                            id="StartTimer"><i class="fa fa-clock-o pr-2" aria-hidden="true"></i><br>{{
                                                $t('StartTimer')
                                            }}</a>
                                        <a v-on:click="openStopPopUp()"
                                            v-if="TicketData.isFieldServiceTicket && AddEditText === 'Edit' && ShowWorkOrderTimer === 'yes'"
                                            v-bind:class="{ 'disabled': isDisabledHeaderButton, 'd-none': ShowWorkOrderTimer == 'no' }"
                                            href="javascript:;" class="p-action-btn text-white" :title="$t('StopTimer')"
                                            id="StopTimer"><i class="fa fa-clock-o pr-2" aria-hidden="true"></i><br>{{
                                                $t('StopTimer')
                                            }}</a>

                                        <!-- <a href="javascript:;" class="p-action-btn text-white" v-show="isViewType" v-on:click="changeWorkOrderView()" :title="$t('StopTimer')" id="ViewType"><i :class="classView" aria-hidden="true"></i><br>{{viewType}}</a> -->

                                        <RouterLink href="javascript:;" v-if="SourcePage == 'Ticketing'" to="/Ticketing/Index"
                                            class="p-action-btn text-white d-none d-md-inline-block"
                                            :title="$t('BacktoList')">
                                            <em class="fa fa-angle-double-left" aria-hidden="true"></em><br />{{
                                                $t('BacktoList') }}
                                        </RouterLink>
                                        <RouterLink href="javascript:;" v-else-if="SourcePage == 'PendingAcceptance'" to="/Ticketing/PendingAcceptance"
                                            class="p-action-btn text-white d-none d-md-inline-block"
                                            :title="$t('BacktoList')">
                                            <em class="fa fa-angle-double-left" aria-hidden="true"></em><br />{{
                                                $t('BacktoList') }}
                                        </RouterLink>
                                    </span>
                                    <span class="collapse-head-btns">
                                        <a href="javascript:;" class="collapse-btns"><em
                                                class="fa fa-angle-up text-white"></em></a>
                                    </span>
                                </div>
                            </div>
                            <div class="bg-secondary text-white p-2 d-block d-md-none">{{ TicketNumber }}</div>
                            <div class="clearfix"></div>
                        </div>
                        <div id="divticketdetaildleftpanel" :class="{ 'listing': true }">
                            <div id="divDetails">
                                <ticketdetails v-if="isShowTicketDetail" :TicketId="TicketId" :ListOfCCUsers="ListOfCCUsers"
                                    :TicketDetailDataForPopup="TicketDetailDataForPopup" :TicketData="TicketData">
                                </ticketdetails>
                            </div>

                            <div class="clearfix"></div>
                            <div class="listing tab-content">
                                <div class="col-12 d-flex border border-bottom-0 bg-f5f5f5" v-if="isShowActivities"
                                    id="divconversationbox">
                                    <div class="col-md-6 pl-0">
                                        <h6 class="py-3 mb-0">
                                            {{ $t('Activities') }}
                                        </h6>
                                    </div>
                                    <div class="col-md-6 text-right pr-0 bottom_filter_button">
                                        <a href="javascript:;" id="ancreplyExpandAllCollapseAll" class="position-relative"
                                            style="z-index:999;" :title="$t('ExpandAllCollapseAll')"
                                            data-toggle-tooltip="tooltip">{{ $t('ExpandAllCollapseAll') }}</a>
                                    </div>
                                </div>
                                <div class="tab-pane fade show activity-tab active pt-3" id="tab-ticket-content"
                                    role="tabpanel" aria-labelledby="Activities-tab">
                                    <div class="d-md-inline-block w-100">
                                        <!-- Merged Ticket -->
                                        <div class="merged-ticket" style="display: none;">
                                            <div class="merged-ticket-header">{{ $t('MergedTickets') }}</div>
                                            <div class="merged-ticket-content">
                                            </div>
                                        </div>
                                        <!-- / Merged Ticket -->
                                        <div class="cont_100P" id="divloadticketdata">
                                        </div>

                                        <ThreadsList v-if="isShowActivities" :TicketThreads="TicketThreadsData">
                                        </ThreadsList>

                                        <div class="cont_100P" id="divSplitConversationBox">
                                        </div>
                                    </div>
                                </div>
                                <div v-if="UserType != 'CL'" class="tab-pane fade" id="tab-notes-content" role="tabpanel"
                                    aria-labelledby="Notes-tab">
                                    <TicketNotes v-if="isShowNotes" :NoteData="NoteData"></TicketNotes>
                                </div>
                                <div class="tab-pane fade" id="tab-attachment-content" role="tabpanel"
                                    aria-labelledby="Attachment-tab">
                                    <div id="div_ShowGpxFile" class="col-12 p-0 d-flex flex-wrap"></div>
                                    <div class="listing pt-1">
                                        <TicketAttachment :AttachmentData="AttachmentData" @open-image="openSpecificImage"
                                            viewMode="AttachmentInDetail"></TicketAttachment>
                                    </div>
                                    <!-- <TicketAttachment v-if="isShowAttchments" :AttachmentData="AttachmentData"></TicketAttachment> -->
                                </div>
                                <div class="tab-pane fade" id="tab-cliam-content">
                                    <TicketClaim v-if="isShowClaims" :ClaimData="ClaimData"></TicketClaim>
                                </div>
                                <div class="tab-pane fade" id="tab-call-content">
                                    <TicketCalls v-if="isShowCalls" :CallData="CallData"></TicketCalls>
                                </div>

                                <div class="tab-pane fade" id="tab-relatedticket-content">
                                    <RelatedTicketList v-if="isShowRelatedticket" :ticketId="TicketId"></RelatedTicketList>
                                </div>

                                <div class="tab-pane fade" id="tab-trackhistory-content">
                                    <TrackHistoryList v-if="isShowTrackHistory" :ticketId="TicketId"></TrackHistoryList>
                                </div>

                                <div class="tab-pane fade" id="tab-workorder-content">
                                    <WorkOrderList v-if="isShowWorkOrder" :isLocateorNonLocate="isLocateorNonLocate"
                                        :ticketId="TicketId" v-bind:callbackfunction="FetchData"></WorkOrderList>
                                </div>
                            </div>
                        </div>
                    </div>
                    <VueEasyLightbox ref="lightbox" :show="true" v-if="visible" v-bind:images="galleryImages"
                        @close="visible = false" :customClasses="lightboxClasses"></VueEasyLightbox>
                </div>
                <!-- start left panel -->


            </div>

        </section>
        <FollowUp v-if="isshowFolloupList" ticketStatus="" :ticketId="TicketId"
            v-on:closeFollowup="isshowFolloupList = false"></FollowUp>
        <AddNoteData v-if="isShowAddNote" :id="TicketId" modulename="Ticketing" submodulecode="Ticketing" viewMode="Notes"
            v-bind:callbackfunction="GetNoteData" :configdata="ConfigData" :HeaderName="$t('AddNotes')"></AddNoteData>

        <modalReplyTicketpopup v-if="showReplyTicketModal" v-on:close="showReplyTicketModal = false" :id="TicketId"
            v-bind:callbackfunction="FetchData" modulename="Ticketing" submodulecode="Ticketing"></modalReplyTicketpopup>

        <AddEscalatePopUp v-if="isShowEscalate" v-on:close="isShowEscalate = false" :ticketId="TicketId"
            :ParentCompanyId="ParentCompanyId" :ParentCompanyName="ParentCompanyName"></AddEscalatePopUp>

        <!-- <AddNote v-if="isShowAddNote" :HeaderName="$t('AddNotes')" :sourceid=
            "TicketData.ticketId" @close="isShowAddNote=false"></AddNote> -->
        <ShowAttachmentData v-if="showAttachmentModal" v-on:close="showAttachmentModal = false" :id="TicketId"
            viewMode="Attachments" v-bind:callbackfunction="GetAttachmentData" modulename="Ticketing"
            :configdata="ConfigData" submodulecode="Ticketing">
            <h5 slot="header">{{ $t('Attachments') }}</h5>
        </ShowAttachmentData>
        <AddClaimData v-if="isShowAddClaim" :id="TicketId" :claimId="TicketData.claimId" modulename="Ticketing"
            submodulecode="Ticketing" viewMode="Claims" v-bind:callbackfunction="GetClaimData"
            :HeaderName="$t('AddClaim')">
        </AddClaimData>

        <ticketupdate v-if="isShowTicketUpdate" :TicketId="TicketId" modulename="Ticketing" submodulecode="Ticketing"
            v-bind:callbackfunction="FetchData" @close="isShowTicketUpdate = false"></ticketupdate>
        <transfertickets v-if="isShowTransferTicket" :TicketId="TicketId" modulename="Ticketing" submodulecode="Ticketing"
            v-bind:callbackfunction="FetchData" @close="isShowTransferTicket = false"></transfertickets>
        <MergeTicketData v-if="isShowMergeTicket" :str_ticket_id="str_ticket_id" :id="TicketId" modulename="Ticketing"
            submodulecode="Ticketing" v-bind:callbackfunction="FetchData"></MergeTicketData>

        <WorkOrderTimerPopup v-if="isShowWOTimer" :id="TicketId" modulename="Ticketing" submodulecode="Ticketing"
            viewMode="Timer" v-bind:callbackfunction="FetchData"></WorkOrderTimerPopup>
    </section>
</template>

<script>
import DataServices from "@/services/DataService";
import ticketdetails from "../../components/Modal/modalTicketDetails.vue";
import AddEscalatePopUp from "@/components/Modal/AddEscalateModalPopUp.vue";
import ThreadsList from "@/components/Modal/ThreadsList.vue";
import TicketNotes from "@/components/Modal/TicketNotes.vue";
import TicketAttachment from "@/components/Modal/TicketAttachment.vue";
import TicketClaim from "./TicketClaim.vue";
import TicketCalls from "./TicketCalls.vue";
import RelatedTicketList from "./DetailsComponent/RelatedTicketList.vue"
import TrackHistoryList from "./DetailsComponent/TrackHistoryList.vue"
import WorkOrderList from "./DetailsComponent/WorkOrderList.vue"
import FollowUp from '@/components/Modal/AddFollowUpList.vue';
import AddClaimData from "@/components/Modal/AddClaimModal.vue";
import MergeTicketData from "@/components/Modal/MergedTicketModal.vue";
import AddNoteData from "../../views/Ticketing/AddNoteData.vue";
import ShowAttachmentData from "../../views/Ticketing/ShowAttachmentData.vue";
import ticketupdate from "./Update.vue";
import transfertickets from "./AssignTransferTicket.vue";
import MapComponent from "../../components/Common/MapComponent.vue";
import modalReplyTicketpopup from '../../components/Modal/modalReplyTicket.vue';
import WorkOrderTimerPopup from "@/components/Modal/WorkOrderTimerModal.vue";
import VueEasyLightbox from 'vue-image-lightbox-carousel';
export default {
    components: {
        ticketdetails,
        ThreadsList,
        TicketNotes,
        TicketAttachment,
        AddEscalatePopUp,
        TicketClaim,
        TicketCalls,
        FollowUp,
        ShowAttachmentData,
        AddNoteData,
        AddClaimData,
        MergeTicketData,
        ticketupdate,
        transfertickets,
        MapComponent,
        modalReplyTicketpopup,
        WorkOrderTimerPopup,
        RelatedTicketList,
        TrackHistoryList,
        WorkOrderList,
        VueEasyLightbox
    },
    data() {
        return {
            config: {
                isShowDrawButton: false,
                isShowEtaMap: true,
                isShowLocator: false,
                isShowEtaPipeLines: false,
                isShowGetDirection: true
            },
            isLoading: false,
            TicketId: null,
            ticketId: null,
            TicketNumber: null,
            TicketData: [],
            TicketDetailData: [],
            UserType: null,
            isTicketResolve: null,
            galleryImages: [],
            ConfigData: [],
            ShowWorkOrderTimer: "no",
            isTicketHOD: null,
            TicketThreadsData: [],
            AttachmentData: [],
            NoteData: [],
            visible: false,
            ClaimData: [],
            CallData: [],
            userstatus: '',
            isShowTicketDetail: false,
            isShowActivities: false,
            isShowAddClaim: false,
            isShowEscalate: false,
            isShowNotes: false,
            isShowClaims: false,
            isShowCalls: false,
            isShowRelatedticket: false,
            isshowFolloupList: false,
            isShowAddNote: false,
            isShowMergeTicket: false,
            isShowTicketUpdate: false,
            isShowTransferTicket: false,
            showAttachmentModal: false,
            showReplyTicketModal: false,
            isShowTrackHistory: false,
            isShowWorkOrder: false,
            isShowWOTimer: false,
            UserStatus: null,
            mapObj: [],
            UtilityMapData: [],
            ticketType: null,
            isShowMap: true,
            mapHeaderText: 'Collapse Map',
            TicketDetailDataForPopup: [],
            PageNumber: 1,
            PageSize: 10,
            TotalRecords: 0,
            ParentCompanyId: null,
            ParentCompanyName: null,
            ShowTimer: false,
            AddEditText: '',
            SortBy: '',
            SortExp: '',
            noRecord: false,
            index: 0,
            isLocateorNonLocate: '',
            ListOfCCUsers: '',
            ticketStatus: '',
            isDisabledHeaderButton: false,
            mapType: null,
            SourcePage: null
        }
    },
    mounted() {
        debugger
        var vm = this;
        const sourcePage = this.$route.query.source;
        vm.SourcePage = sourcePage;
    },
    created() {
        var vm = this;
        vm.str_ticket_id = vm.$route.params.id;
        vm.UserType = vm.GetUserInfo.UserType;
        vm.FetchData();
        vm.UserCurrentStatus();

    },
    updated: function () {
        var vm = this;
        setTimeout(function () {
            vm.setHeaderForSelectedTab();
        }, 500);

        $("#ancreplyExpandAllCollapseAll").click(function () {
            if ($(this).attr("show") == "0") {
                $(this).attr("show", "1");
                $("#tab-ticket-content").find("#accordion").find('.collapse').addClass('show');
            }
            else {
                $(this).attr("show", "0");
