        FetchData: async function () {
            debugger;
            var vm = this;
            vm.isLoading = true;
            var url = `str_ticket_id=${vm.str_ticket_id}&moduleName=Ticketing&subModuleCode=Ticketing`;
            await DataServices.GetTicketDetails(url).then(function (response) {
                debugger;
                vm.isLoading = false;
                let ticketData = {};
                vm.ticketType = response.data.result.IS_FIELD_SERVICE;
                vm.ConfigData = response.data.result.CONFIG_DATA;
                if (response.data.result.LEFTDATA.length > 0) {
                    var result = response.data.result.LEFTDATA;
                    vm.TicketDetailDataForPopup = response.data.result.LEFTDATA;
                    vm.UtilityMapData = response.data.result.FSM_MAP_CONFIG;
                    vm.mapType = response.data.result.FSM_MAP_CONFIG[0].TYPE;
                    result.forEach(function (row) {
                        if (row.name == "ticket_id") {
                            ticketData["ticketId"] = row.value;

                        }
                        else if (row.name == "sla_id") {
                            ticketData["TicketSlaId"] = row.value;
                        }
                        else if (row.name == "ticket_number") {
                            ticketData["ticket_number"] = row.value;
                        }
                        else if (row.name == "created_by") {
                            ticketData["createdBy"] = row.value;
                        }

                        else if (row.name == "assigned_to") {
                            ticketData["assignedTo"] = row.value;
                        }
                        else if (row.name == "external_ticket_number") {
                            ticketData["external_ticket_number"] = row.value;
                        }
                        else if (row.name == "ticket_subject") {
                            ticketData["ticketSubject"] = row.value;
                        }
                        else if (row.name == "status_id") {
                            ticketData["status"] = row.value;
                        }
                        else if (row.name == "ticket_category_id") {
                            ticketData["ticketCategoryName"] = row.value;
                        }
                        else if (row.name == "priority_id") {
                            ticketData["priority"] = row.value;
                        }
                        else if (row.name == "created_at") {
                            ticketData["createdAt"] = row.value;
                        }
                        else if (row.name == "expected_resolution_date") {
                            ticketData["expectedResolutionDate"] = row.value;
                        }
                        else if (row.name == "Description") {
                            ticketData["description"] = row.value;
                        }
                        else if (row.name == "location_nw") {
                            ticketData["location_nw"] = row.value;
                        }
                        else if (row.name == "location_se") {
                            ticketData["location_se"] = row.value;
                        }
                        else if (row.name == "created_by") {
                            ticketData["createdBy"] = row.value;
                        }

                    });
                    ticketData["noOfNotes"] = response.data.result.NO_OF_NOTES;
                    ticketData["attachmentsCount"] = response.data.result.ATTACHMENTS_COUNT;
                    ticketData["noOfClaims"] = response.data.result.NO_OF_CLAIM;
                    ticketData["isFieldServiceTicket"] = response.data.result.IS_FIELD_SERVICE == 1 ? true : false;
                    ticketData["departmentName"] = response.data.result.DEPARTMENT_NAME;
                    ticketData["followUpId"] = response.data.result.FOLLOW_UP_ID;
                    ticketData["raisedBy"] = response.data.result.RAISED_BY;
                    ticketData["raisedByDepartmentName"] = response.data.result.DEPT_NAME;
                    ticketData["createdUserTypeId"] = response.data.result.TICKET_CREATED_USER_TYPE;
                    vm.isLocateorNonLocate = response.data.result.IS_FSM_LOCATING;
                    vm.ListOfCCUsers = response.data.result.CC_USERS_NON_LOCATE;
                    //    vm.isLocateorNonLocate = true;
                    vm.TicketData = ticketData;
                    vm.TicketNumber = vm.TicketData.ticket_number;
                    vm.TicketId = vm.TicketData.ticketId;
                    console.log("TicketId", vm.TicketId);
                    vm.ParentCompanyId = response.data.result.PARENT_COMPANY_ID;
                    vm.ParentCompanyName = response.data.result.PARENT_COMPANY_NAME;
                    debugger;
                    if (vm.TicketData.status == 'Closed' || vm.TicketData.status == 'Closed and Locked' || vm.TicketData.status == 'Cancelled' || vm.TicketData.status == 'Completed') {
                        vm.isDisabledHeaderButton = true;
                    }
                    vm.isTicketResolve = response.data.result.IS_TICKET_RESOLVE;
                    if (response.data.result.SHOW_Work_Order_Timer.CONFIGURATION_Work_Order_Timer == 'yes') {
                        vm.ShowWorkOrderTimer = 'yes'
                    }
                    else {
                        vm.ShowWorkOrderTimer = 'no'
                    }

                    if (response.data.result.IN_COMPLETE_ENTRY == '1') {
                        vm.AddEditText = "Edit";
                    }
                    vm.isTicketHOD = response.data.result.IS_TICKET_HOD;
                    vm.UserStatus = response.data.result.STATUS_CODE.toLowerCase();
                    setTimeout(function () {
                        vm.LoadTab('activities');
                    }, 500);
                    var obj = {
                        latlong: vm.TicketData.location_nw,
                        refValue: vm.TicketNumber,
                        popupHeaderHtml: '',
                        popupHeaderColor: "",
                        popupBodyHtml: '',
                        popupFooterHtml: '',
                        pinLatLong: {
                            latitude: vm.TicketData.location_nw,
                            longitude: vm.TicketData.location_nw
                        },
                        polygons: [
                            {
                                name: "",
                                latLong: [{
                                    latitude: vm.TicketData.location_nw,
                                    longitude: vm.TicketData.location_nw,
                                }
                                ]
                            }
                        ],
                        address: "",
                        priority: '',
                        color_code: ''
                    }

                    vm.mapObj.push(obj);
                }


            })
        },
