                }
            });

        },
        GetCallData: async function () {
            var vm = this;
            vm.isLoading = true;
            var url = `ticketId=${vm.TicketId}&subModuleCode=Ticketing&reqFrom=`;
            await DataServices.GetCallList(url).then(function (response) {
                vm.isLoading = false;
                debugger;
                if (response.data != null) {
                    vm.CallData = response.data;
                }
                else {
                    vm.CallData = [];
                }
            });

        },
        openAttachmentModel: function () {
            var vueObj = this;
            vueObj.showAttachmentModal = true;
            $("#divAttachment").scrollTop(0);
        },

        GetAttachmentData: function () {
            //debugger;
            var vueObj = this;
            vueObj.galleryImages = [];
            vueObj.AttachmentData = [];
            vueObj.galleryFilesView = [];
            vueObj.isLoading = true;
            var url = `ticketId=${vueObj.TicketId}`;
            DataServices.GetTicketFiles(url).then(function (response) {
                vueObj.isLoading = false;
                debugger;
                if (response.data.result !="" && response.data.result.DATA.length > 0) {
                    vueObj.AttachmentData = response.data.result.DATA;
