<template>
    <div class="listing">
        <div id="accordion" class="w-100">
            <!-- {{ groupByDate }} -->
          <div class="card" v-for="(groupItem,key,index) in  groupByDate">
                <div class="card-header">
                    <a :class="{'card-link collapsed' : index > 1, 'card-link' : index <= 1}" data-toggle="collapse" :href="'#collapseOneThread'+index">
                        <div class="conversation-date heading-border"><span>{{$t(key)}}</span></div>
                    </a>
                </div>
                <div>
                    <div :id="'collapseOneThread'+index" :class="{'collapse':index > 1 , 'collapse show' : index <= 1}">               
                        <div class="card-body">
                            <div class="conversation-main">
                    
                                <template v-for="(item,index) in groupItem" >
                                    <div class="conversation-time"><span>{{$options.filters.formatTime(item.createdAt)}}</span>
                                    </div>
                                    <div class="conversation-box-con">
                                        <div class="conversation-box">
                                            <div class="name-circle text-white light-yellow"><em class="fa fa-file" aria-hidden="true" style="font-size: 18px; margin-top: 18px !important"></em ></div>
                                            <div class="conversation-header addednote-ticket">
                                                <span>
                                                    <span class="sender-name">{{item.actionPerformedByName}}</span>
                                                    <span class="sender-department">({{item.actionPerformedByDeptName}})</span> 
                                                </span>
                                                
                                                <span class="header-action" v-if="item.ticketAttachmentId>0">
                                                    <a :id="item.ticketAttachmentId" @click="DownloadAttachment(item)" class="btn-primary downloadAttachement round-icon-split round-icon split" :title="$t('ClickHereDownloadAttachment')" :alt="$t('DownloadFile')" href="javascript:;" :value="item.ticketAttachmentId" :data-isSupportTicket="item.isSupportTicket">
                                                        <em class="fa fa-download"></em >
                                                    </a>
                                                </span>
                                            </div>
                                            <div class="conversation-content addednote-ticket">
                                                <p>{{ SetMessage(item) }}</p>
                                                    <div class="addednote-ticket-content" v-if="item.threadDescription != null && item.threadDescription.length > 0">
                                                        <div class="col-12 p-0 notesdata">
                                                            <div class="readmore">
                                                                <span v-html="item.threadDescription"></span>
                                                            </div>
                                                        </div>
                                                    </div>
                                            </div>
                                        </div>
                                    </div>
                                </template>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-sm-12 col-md-12 text-center col-lg-12 mt-2 float-left p-0" v-if="NoRecords">
                <span class="text-danger no-record">{{ $t('NoRecordfound') }}</span>
            </div>
        </div>
    </div>
</template>

<script>
import DataServices from "@/services/DataService";
export default{
    props:{
        TicketThreads:{
            type:Array,
            required:true
        }
    },
    data(){
        return{
            ThreadsList:[],
            UserType:null,
            UserId:null,
            NoRecords:false
        }
        
    },
    computed: {
        groupByDate() {
         ////debugger;
        var vm=this;
        let group = {};
        vm.TicketThreads.forEach(item => {
            let data = item.createdAt.substring(0, 10);
            if (group[data]) {
                group[data].push(item);
            } else {
                group[data] = [];
                group[data].push(item);
            }
        });
        // if(vm.TicketThreads.length == 0 )
        // {
        //     vm.NoRecords=true;
        // }
        return group;
    }
  },
    created(){
        var vm = this;
        vm.UserId=vm.GetUserInfo.ID;
        vm.UserType=vm.GetUserInfo.UserType;        
    },
    methods:{
        DownloadAttachment(item){           
            var vm=this;
            vm.isLoading=true;
            var param=`filePath=${item.fileSrc}&fileName=${item.fileName}`;
            DataServices.DownloadFile(param).then(function (response) {
                vm.isLoading=false;
                const byteData=response.data;
                const fileURL = `data:${((JSON.parse(item.fileMime)).ContentType)};base64,${byteData}`;
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;
                fileLink.setAttribute('download', item.fileName);
                document.body.appendChild(fileLink);
                fileLink.click();
            });     
        },
        SetMessage : function(item){
            // //debugger;
            var vm =this;
            var message='';
            if(item.statusCode=='TICKET_TRANSFER' && item.currentStatus==item.priviousStatus && vm.UserType !='CA'){
                var statusCode = "TICKET_TRANSFER";
                if(item.threadType=='A')
                {
                    statusCode='TICKET_ASSIGNED';
                }
                if(item.threadCreatedBy==vm.UserId && vm.UserType !='CA' && vm.UserType !='SA'){
                    if(item.ticketCreatedBy==vm.UserId && vm.UserType !='CA' && vm.UserType !='SA'){
                        message= vm.$t(statusCode) + " " + vm.$t('By') + " " + item.departmentName +" "+ vm.$t('To') +" "+ (item.currentStatus=='' ? item.departmentName : item.currentStatus);
                    }
                    else{
                        message= vm.$t(statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName +" "+ vm.$t('To') +" "+ (item.currentStatus=='' ? item.departmentName : item.currentStatus);
                    }
                }
                else{
                    if(vm.UserType!='CA' && vm.UserType !='SA'){
                        message= vm.$t(statusCode) + " " + vm.$t('By') + " " + (statusCode == 'TICKET_TRANSFER' ? item.actionPerformedByDeptName : item.actionPerformedByName) +" "+ vm.$t('To') +" "+ (statusCode == 'TICKET_TRANSFER' ? item.departmentName : item.currentStatus);
                    } 
                    else if(item.currentStatus==''){
                        message= vm.$t(statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName +" "+ vm.$t('To') +" "+ item.departmentName;
                    }  
                    else{
                        message= vm.$t(statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName +" "+ vm.$t('To') +" "+ (item.currentStatus=='' ? item.departmentName : item.currentStatus);
                    }
                }
            }
            else if(item.statusCode=='TICKET_TRANSFER' && item.priviousStatus.length > 0 && vm.UserType !='CL'){
                var statusCode = "TICKET_TRANSFER";
                if (item.threadType == "A")
                {
                    statusCode = "TICKET_ASSIGNED";
                }
                var ChkName='';
                if(item.actionPerformedByName != item.priviousStatus && item.priviousStatus !=''){
                    ChkName= vm.$t('From') +" "+ item.priviousStatus;
                }
                if(item.currentStatus.toUpperCase()== item.priviousStatus.toUpperCase()){
                    ChkName='';
                }
                if(item.threadCreatedBy==vm.UserId && vm.UserType!='CA' && vm.UserType !='SA'){
                    if(item.ticketCreatedBy==vm.UserId && vm.UserType!='CA' && vm.UserType !='SA'){
                        message= vm.$t(statusCode) + " " + vm.$t('By') + " " + item.departmentName +" "+ vm.$t('To') +" "+ (item.currentStatus=='' ? item.departmentName : item.currentStatus) +" "+ ChkName;
                    }
                    else{
                        message= vm.$t(statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName +" "+ vm.$t('To') +" "+ (item.currentStatus=='' ? item.departmentName : item.currentStatus) +" "+ ChkName;
                    }
                }
                else{
                    if(vm.UserType!='CA' && vm.UserType!='SA'){
                        message= vm.$t(statusCode) + " " + vm.$t('By') + " " + statusCode == 'TICKET_TRANSFER' ? item.actionPerformedByDeptName : item.actionPerformedByName +" "+ vm.$t('To') +" "+ (statusCode == 'TICKET_TRANSFER' ? item.departmentName : item.currentStatus) +" "+ ChkName;
                    }
                    else{
                        if(item.isSupportTicket && item.threadCreatedBy !=vm.UserId && vm.UserType !='SA'){
                            message= vm.$t(statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName +" "+ vm.$t('To') +" "+ item.departmentName +" "+ ChkName;
                        }
                        else{
                            message= vm.$t(statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName +" "+ vm.$t('To') +" "+ (item.currentStatus=='' ? item.departmentName : item.currentStatus) +" "+ ChkName;
                        }
                    }
                }
            }
            else if(item.statusCode=='TICKET_TRANSFER' && item.priviousStatus.length <= 0 && vm.UserType !='CL'){
                var statusCode = "TICKET_TRANSFER";
                if (item.threadType == "A")
                {
                    statusCode = "TICKET_ASSIGNED";
                }
                var ChkName = "";
                if(item.actionPerformedByName !=item.priviousStatus && item.priviousStatus !=''){
                    ChkName= vm.$t('From') + " " + item.priviousStatus;
                }
                if(item.currentStatus==item.priviousStatus){
                    ChkName="";
                }
                if(item.threadCreatedBy==vm.UserId && vm.UserType!='CA' && vm.UserType != "SA"){
                    if(item.ticketCreatedBy==vm.UserId && vm.UserType!='CA' && vm.UserType != "SA"){
                        message= vm.$t(statusCode) + " " + vm.$t('By') + " " + item.departmentName +" "+ vm.$t('To') +" "+ (item.currentStatus=='' ? item.departmentName : item.currentStatus) +" "+ ChkName;
                    }
                    else{
                        message= vm.$t(statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName +" "+ vm.$t('To') +" "+ (item.currentStatus=='' ? item.departmentName : item.currentStatus) +" "+ ChkName;
                    }
                }
                else{
                    if(vm.UserType !='CA' && vm.UserType != 'SA'){
                        var CurrentStatus = item.currentStatus;
                        if(CurrentStatus ==''){
                            CurrentStatus=item.TicketThreadTo;
                        }
                        if(item.departmentName.toUpperCase()==CurrentStatus.toUpperCase()){
                            message= vm.$t(statusCode) + " " + $t('By') + " " + item.departmentName;
                        }
                        else{
                            message= vm.$t(statusCode) + " " + $t('By') + (statusCode =='TICKET_TRANSFER' ? item.actionPerformedByDeptName : item.actionPerformedByName) + " " + vm.$t('To') + " " + (statusCode=='TICKET_TRANSFER' ? item.departmentName : CurrentStatus + " " + ChkName)
                        }
                    }
                    else{
                        message= vm.$t(statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName +" "+ vm.$t('To') +" "+ (item.currentStatus=='' ? item.departmentName : item.currentStatus) +" "+ ChkName;
                    }
                }
            }
            else if((item.statusCode=='TICKET_REPLY_SENT' || item.statusCode=='TICKET_REPLY_DRAFT') && item.priviousStatus.length > 0){
                debugger;
                if(vm.UserType == 'CL' && item.statusCode=='TICKET_REPLY_DRAFT'){
                    message= vm.$t(item.statusCode) + " "+ vm.$t('By') + " " + item.actionPerformedByName;
                }
                else if(vm.UserType=='CL' && item.threadCreatedBy==vm.UserId){
                    message= vm.$t('TICKET_REPLY_CL_SEND',[item.ticketNumber]);
                }
                else if(vm.UserType=='CL'){
                    message=vm.$t('TICKET_REPLY_CL',[item.ticketNumber]);
                }
                else
                {
                    if (item.threadCreatedBy == vm.UserId && vm.UserType != "CA" && vm.UserType != "SA")
                    {
                        if (item.ticketCreatedBy == vm.UserId && vm.UserType != "CA" && vm.UserType != "SA")
                        {
                            if (item.statusCode.toUpperCase() == item.departmentName.toUpperCase())
                            {
                                item.departmentName = item.actionPerformedByName;
                            }
                            message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.departmentName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus) + " " + vm.$t('And') + " " + vm.$t('Cc') + " " + item.priviousStatus;
                        }
                        else
                        {
                            message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus) + " " + vm.$t('And') + " " + vm.$t('Cc') + " " + item.priviousStatus;
                        }
                    }
                    else
                    {                                                                                             
                        if (vm.UserType != "CA" || vm.UserType != "SA")
                        {
                            if (item.statusCode.toUpperCase() == item.departmentName.toUpperCase())
                            {
                                item.departmentName = item.actionPerformedByName;
                            }
                            if (item.statusCode.toUpperCase() == item.actionPerformedByName.toUpperCase())
                            {
                                message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + (item.currentStatus=='' ? "--" : item.currentStatus) + " " + vm.$t('To') + " " + item.departmentName + " " + vm.$t('And') + " " + vm.$t('Cc') + " " + item.priviousStatus;
                            }
                            else if (item.isSupportTicket)
                            {
                                message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus) + " " + vm.$t('And') + " " + vm.$t('Cc') + " " + item.priviousStatus;
                            }
                            else
                            {
                                message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.departmentName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus) + " " + vm.$t('And') + " " + vm.$t('Cc') + " " + item.priviousStatus;
                            }
                        }
                        else
                        {
                            message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus) + " " + vm.$t('And') + " " + vm.$t('Cc') + " " + item.priviousStatus;
                        }
                    }
                }
                
            }
            else if ((item.statusCode == "TICKET_REPLY_SENT" || item.statusCode == "TICKET_REPLY_DRAFT") && item.priviousStatus.length <= 0)
            {
                debugger;
                if (vm.UserType == "CL" && item.statusCode == "TICKET_REPLY_DRAFT")
                {
                    message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName;
                }
                else if (vm.UserType == "CL" && item.threadCreatedBy == vm.UserId)
                {
                    message= vm.$t('TICKET_REPLY_CL_SEND',[item.ticketNumber]);
                }
                else if (vm.UserType == "CL")
                {
                    message= vm.$t('TICKET_REPLY_CL',[item.ticketNumber]);
                }
                else
                {
                    var CurrentStatus = item.currentStatus;
                    if (item.currentStatus == item.departmentName || CurrentStatus=='')
                    {
                        CurrentStatus = item.TicketThreadTo;
                    }
                    if (item.isSupportTicket && item.threadCreatedBy != vm.UserId && vm.UserType != "SA")
                    {
                        message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.departmentName + " " + vm.$t('To') + " " + (item.currentStatus == '' ? "--" : item.currentStatus);
                    }
                    else
                    {
                        if (item.actionPerformedByName.toUpperCase() == item.currentStatus.toUpperCase())
                        {
                            message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + item.departmentName;
                        }
                        else
                        {
                            message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus);
                        }
                    }
                    
                }
            }
            else if (item.statusCode == "TICKET_SPLIT")
            {
                message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName;
            }
            else if (item.statusCode == "TICKET_STATUS_CHANGE" && item.currentStatus == item.priviousStatus)
            {
                if (vm.UserType == "CL")
                {
                    message= vm.$t('TICKET_STATUS_CHANGE_CL',[item.ticketNumber]);
                }
                else
                {
                    if (item.threadCreatedBy == vm.UserId && vm.UserType != "CA" && vm.UserType != "SA")
                    {
                        if (item.ticketCreatedBy == vm.UserId && vm.UserType != "CA" && vm.UserType != "SA")
                        {
                            if (item.threadCreatedBy == vm.UserId && vm.UserType != "CL")
                            {
                                message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus);
                            }
                            else
                            {
                                if (item.threadCreatedBy == item.ticketCreatedBy)
                                {
                                    message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus);
                                }
                                else
                                {
                                    message = vm.$t(item.statusCode) + " " + vm.$t('By') +  " " + item.departmentName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus);
                                }
                            }
                        }
                        else
                        {
                            message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus);
                        }
                    }
                    else
                    {
                        if (vm.UserType != "CA" && vm.UserType != "SA")
                        {
                            if (item.threadCreatedBy == vm.UserId && vm.UserType != "CL")
                            {
                                message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus);
                            }
                            else
                            {
                                if (item.threadCreatedBy == item.ticketCreatedBy)
                                {
                                    message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus);
                                }
                                else
                                {
                                    message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.departmentName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus);
                                }
                            }
                        }
                        else
                        {
                            message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus);
                        }
                    }
                }
            }
            else if (item.statusCode == "TICKET_STATUS_CHANGE" && item.priviousStatus.length > 0)
            {
                if (vm.UserType == "CL")
                {
                    message= vm.$t('TICKET_STATUS_CHANGE_CL',[item.ticketNumber]);
                }
                else
                {
                    if (item.threadCreatedBy == vm.UserId && vm.UserType != "CA" && vm.UserType != "SA")
                    {
                        if (item.ticketCreatedBy == vm.UserId && vm.UserType != "CA" && vm.UserType != "SA")
                        {
                            if (item.threadCreatedBy == vm.UserId && vm.UserType != "CL")
                            {
                                message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus) + " " + vm.$t('From') + " " + item.priviousStatus; ;
                            }
                            else
                            {
                                if (item.threadCreatedBy == item.ticketCreatedBy)
                                {
                                    message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus) + " " + vm.$t('From') + " " + item.priviousStatus ;
                                }
                                else
                                {
                                    message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.departmentName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus) + " " + vm.$t('From') + " " + item.priviousStatus ;
                                }
                            }
                        }
                        else
                        {
                            message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus) + " " + vm.$t('From') + " " + item.priviousStatus ;
                        }
                    }
                    else
                    {
                        if (vm.UserType != "CA" && vm.UserType != "SA")
                        {
                            if (item.threadCreatedBy == vm.UserId && vm.UserType != "CL")
                            {
                                message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus) + " " + vm.$t('From') + " " + item.priviousStatus ;
                            }
                            else
                            {
                                if (item.threadCreatedBy == item.ticketCreatedBy)
                                {
                                    message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus) + " " + vm.$t('From') + " " + item.priviousStatus ;
                                }
                                else
                                {
                                    message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.departmentName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus) + " " + vm.$t('From') + " " + item.priviousStatus ;
                                }
                            }
                        }
                        else
                        {
                            if (item.isSupportTicket && item.threadCreatedBy != vm.UserId && vm.UserType != "SA")
                            {
                                message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.departmentName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus) + " " + vm.$t('From') + " " + item.priviousStatus ;
                            }
                            else
                            {
                                message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentStatus=='' ? "--" : item.currentStatus) + " " + vm.$t('From') + " " + item.priviousStatus ;
                            }
                        }
                    }
                }
            }
            else if (item.statusCode == "TICKET_CLOSED")
            {
                if (vm.UserType == "CL")
                {
                    message= vm.$t('TICKET_STATUS_CHANGE_CL',[item.ticketNumber]);
                }
                else
                {
                    message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName;
                }
            }
            else if (item.statusCode == "TICKET_FEEDBACK" || item.statusCode == "TICKET_NOTES_CREATED" || item.statusCode == "TICKET_NOTES_DELETED")
            {
                message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName;
            }
            else if (item.statusCode == "TICKET_ETA_CHANGE")
            {
                if (vm.UserType == "CL")
                {
                    message= vm.$t('TICKET_ETA_CHANGE_CL',[item.ticketNumber]);
                }
                else if (item.priviousStatus !='')
                {
                    message = vm.$t("TICKET_ETA_CHANGE_MESSAGE") + " " + item.actionPerformedByName + " " + vm.$t('From') + " " + item.priviousStatus + " " +  vm.$t('To') + " " + item.currentStatus;
                }
                else
                {
                    message = vm.$t("TICKET_ETA_UPDATE_MESSAGE") + " " + item.currentStatus + " " + vm.$t('By') + " " + item.actionPerformedByName;
                }
            }
            else if (item.statusCode == "TICKET_CONCLUSION")
            {
                if (item.currentStatus == "TICKET CONCLUSION DRAFT")
                {
                    message = vm.$t("Conclusion") + " " + vm.$t('Drafted') + " " + vm.$t('By') + " " + item.actionPerformedByName;
                }
                else
                {
                    message = vm.$t("Conclusion") + " " + vm.$t('Created_By') + " " + item.actionPerformedByName;
                }
            }
            else if (item.statusCode == "CLAIM_CREATED" || item.statusCode == "CLAIM_APPROVE" || item.statusCode == "CLAIM_REJECT")
            {
                if (item.currentStatus == "CLAIM DRAFT" || item.currentStatus == "TICKET_CLAIM_DRAFT")
                {
                    message = vm.$t("TICKET_CLAIM_DRAFT") + " " + vm.$t('By') + " " + item.actionPerformedByName;
                }
                else
                    message = vm.$t(item.statusCode) + " " + vm.$t('By')+ " " + item.actionPerformedByName;
            }
            else if (item.statusCode == "TICKET_CREATED" && item.onBehalfUser)
            {
                message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName;
            }
            else if (item.statusCode == "TICKET_CREATED" && item.onBehalfUser!='')
            {
                message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('OnBehalf') + " " + vm.$t('of') + " " + item.onBehalfUser;
            }
            else if (item.statusCode == "TICKET_PRODUCT_CHANGE")
            {
                if (vm.UserType == "CL")
                {
                    message= vm.$t('TICKET_PRODUCT_CHANGE_CL',[item.ticketNumber]);
                }
                else
                {
                    var fromPart = null;
                    if (item.previousProduct !='')
                    {
                        fromPart = " " + vm.$t('From') + " " + item.previousProduct;
                    }
                    if (!vm.isBlank(item.previousProduct) && (item.previousProduct.toLowerCase() == item.currentProduct.toLowerCase()))
                    {
                        message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentProduct=='' ? vm.$t('No') + " " + vm.$t('Product') : item.currentProduct);
                    }
                    else
                    {
                        message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentProduct=='' ? vm.$t('No') + " " + vm.$t('Product') : item.currentProduct) + fromPart; 
                    }
                }
            }
            else if (item.statusCode == "TICKET_CATEGORY_CHANGE")
            {
                if (vm.UserType == "CL")
                {
                    message= vm.$t('TICKET_CATEGORY_CHANGE_CL',[item.ticketNumber]);
                }
                else
                {
                    var fromPart = null;
                    if (item.previousProduct !='')
                    {
                        fromPart = " " + vm.$t('From') + " " + item.previousProduct;
                    }
                    if (!vm.isBlank(item.previousProduct) && (item.previousProduct.toLowerCase() == item.currentProduct.toLowerCase()))
                    {
                        message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentProduct=='' ? vm.$t('No') + " " + vm.$t('Category') : item.currentProduct);
                    }
                    else
                    {
                        message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentProduct=='' ? vm.$t('No') + " " + vm.$t('Category') : item.currentProduct) + fromPart; 
                    }
                
                }
            }
            else if (item.statusCode == "TICKET_PRIORITY_CHANGE")
            {
                if (vm.UserType == "CL")
                {
                    message= vm.$t('TICKET_CATEGORY_CHANGE_CL',[item.ticketNumber]);
                }
                else
                {
                    var fromPart = null;
                    if (item.previousProduct !='')
                    {
                        fromPart = " " + vm.$t('From') + " " + item.previousProduct;
                    }
                    if (!vm.isBlank(item.previousProduct) && (item.previousProduct.toLowerCase() == item.currentProduct.toLowerCase()))
                    {
                        message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentProduct=='' ? vm.$t('No') + " " + vm.$t('Priority') : item.currentProduct);
                    }
                    else
                    {
                    message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('To') + " " + (item.currentProduct=='' ? vm.$t('No') + " " + vm.$t('Priority') : item.currentProduct) + fromPart; 
                    }
                }
            }
            else if (item.statusCode == "TICKET_MERGE")
            {
                message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName;
            }
            else if (item.statusCode == "TICKET_ESCALATES_NEXT_LEVEL")
            {
                message = vm.$t('TICKET_ESCALATES_NEXT_LEVEL',[vm.$t('Level') +" "+ item.currentStatus, item.actionPerformedByName])
            }
            else if (item.statusCode == "TICKET_DELETED")
            {
                if (vm.UserType == "CL")
                {
                    message= vm.$t('TICKET_DELETED_CL',[item.ticketNumber]);
                }
                else
                {
                    message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName;
                }
            }
            else if (item.statusCode == "ADD_ATTACHMENT" || item.statusCode == "TICKET_ACCEPT" || item.statusCode == "TICKET_REJECT" || item.statusCode == "TICKET_WO_ADDED" || item.statusCode == "REPORT_INCIDENT" || item.statusCode == "DELETE_REPORT_INCIDENT" || item.statusCode == "EDIT_REPORT_INCIDENT")
            {
                message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName;
            }
            else if (item.statusCode == "TICKET_WO_SUB_STATUS_CHANGED" || item.statusCode == "WO_STATUS_CHANGE")
            {
                message = vm.$t(item.statusCode) + " " + vm.$t('By') + " " + item.actionPerformedByName + " " + vm.$t('From') + " " + item.previousProduct + " " + vm.$t('To') + " " + item.currentProduct;
            }
            else if (item.statusCode == "TICKET_WO_STATUS_CHANGED_HIGH_PROFILE_WHILE_PARSING")
            {
                message = vm.$t(item.statusCode);
            }
            else if (item.statusCode == "HIGH_PROFILE_EMAIL_NOTIFICATION")
            {
                message = vm.$t(item.statusCode,[item.ticketNumber,item.currentProduct]);
            }
            else if (item.statusCode == "TICKET_ASSIGN_SMS")
            {
                message = vm.$t(("Ticket_Assign_SMS"), [item.ticketNumber,item.currentProduct]);
            }
            else if (item.statusCode == "WORK_ORDER_FORM_NOTIFICATION" || item.statusCode == "CORNER_LOT_EMAIL_NOTIFICATION" || item.statusCode == "GAS_FACILITY_EMAIL_NOTIFICATION" || item.statusCode == "CAST_IRON_EMAIL_NOTIFICATION" || item.statusCode == "GAS_PRINT_ISSUE_EMAIL_NOTIFICATION")
            {
                message = vm.$t(item.statusCode) + " " + item.threadType + " " + vm.$t('By') + " " + item.actionPerformedByName;
            }
            else if (item.statusCode == "PL_FORM_NOTIFICATION_HAS_BEEN_SENT")
            {
                message = item.threadType;
            }
            else if (item.statusCode == "WORK_ORDER_FORMS_ACCEPT")
            {
                message = "Work Order Forms accepted " + vm.$t('By') + " " + item.actionPerformedByName;
            }
            else if (item.statusCode == "WORK_ORDER_FORMS_REJECT")
            {
                message = "Work Order Forms rejected " + vm.$t('By') + " " + item.actionPerformedByName;
            }
            else
            {
                message = vm.$t('AssignedTo') + " " + item.actionPerformedByName + " " + item.departmentName;
            }
            return message;
        }
    }
}
</script>

