<template>
    <section>
        <div class="listing" v-if="!NoRecords">
        <div class="table-responsive table-fix-header">
            <table class="table table-bordered dt-responsive nowrap mt-0">
                <thead class="thead-bg">
                    <tr>
                        <th><span>{{$t('Call') +' '+ $t('Type')}}</span></th>
                        <th><span>{{$t('Call') +' '+ $t('From')}}</span></th>
                        <th><span>{{$t('Call') +' '+ $t('To')}}</span></th>
                        <th><span>{{$t('EventStartTime')}}</span></th>
                        <th><span>{{$t('EventEndTime')}}</span></th>
                        <th><span>{{$t('Duration')}}</span></th>
                        <th><span>{{$t('Comment')}}</span></th>
                        <th><span>{{$t('Status')}}</span></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="list in CallList">
                        <td><span>{{list.CALL_TYPE}}</span></td>
                        <td><span>{{list.CALL_FROM}}</span></td>
                        <td><span>{{list.CALL_TO}}</span></td>
                        <td><span>{{list.START_TIME}}</span></td>
                        <td><span>{{list.END_TIME}}</span></td>
                        <td><span>{{list.DURATION}}</span></td>
                        <td><span>{{list.COMMENT}}</span></td>
                        <td><span class="px-2 bg-success text-white d-block text-center">{{list.STATUS_ID ==1001 ? 'Active' : ''}}</span></td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
    <div class="col-sm-12 col-md-12 col-lg-12 mt-2" v-if="NoRecords">
        <span class="text-danger text-center no-record" style="display:block;">{{$t('NoRecordfound')}}</span>
    </div>
    </section>
</template>

<script>
export default{
    props:{
        CallData:{
            type:Array,
            required:true
        }
    },
    data(){
        return{
            CallList:[],
            NoRecords:false
        }
    },
    created(){
        var vm = this;
        if(vm.CallData.result.data.length > 0){
            vm.CallList = vm.CallData.result.data;
            vm.NoRecords=false;
        }
        else{
            vm.CallList=[];
            vm.NoRecords=true;
        }
    }
}
</script>