<template>
    <section>
        <tg-list ModuleName="Ticketing" SubModuleCode="Ticketing" IdentityColumn="TICKET_ID" :listType="['List']"
            :ListData="TrackHistoryListing" :HeaderData="Headers" :TotalRecords="TotalRecords" :IsShowAction="false"
            :RenderRowActionMethod="() => { }" :ListDataCallBackFunction="FetchRecords"
            :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'WORKORDER_NUMBER'">
                    <RouterLink :title="data.row[data.column.COLUMN_NAME]"
                        :to="'/Ticketing/WorkOrderDetail/' + data.row.WORKORDER_ID + '/' + data.row.TICKET_ID"
                        data-toggle-tooltip="tooltip">
                        <span>
                            <span>#</span>{{ data.row.WORKORDER_NUMBER }}
                        </span>
                    </RouterLink>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'TRACK_START_TIME'">
                    <span>
                        {{ $options.filters.formatDateTime(data.row.TRACK_START_TIME) }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'TRACK_END_TIME'">
                    <span>
                        {{ $options.filters.formatDateTime(data.row.TRACK_END_TIME) }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'TRACK_DESCRIPTION'">
                    <span>
                        <div class="readmore">
                            <span v-html="data.row.TRACK_DESCRIPTION"></span>
                        </div>
                    </span>
                </template>
            </template>
        </tg-list>
    </section>
</template>
<script>

import DataServices from "@/services/DataService";
export default {
    props: {
        ticketId: {
            type: Number,
            required: false
        }
    },
    data() {
        return {
            noRecords: false,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            Headers: [{
                "COLUMN_NAME": "WORKORDER_NUMBER",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "WO Number",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "TRACK_DESCRIPTION",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "DESCRIPTION",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "TRACK_START_TIME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "START TIME",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "TRACK_END_TIME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "END TIME",
                "DISPLAY_ORDER": 4
            },
            {
                "COLUMN_NAME": "DURATION",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Duration (HH:MM:SS)",
                "DISPLAY_ORDER": 5
            }
            ],
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            GroupStart: 1,
            GroupEnd: 3,
            TrackHistoryListing: []

        }
    },
    created: async function () {
        var vm = this;
        await vm.FetchRecords();
    },
    methods: {
        FetchRecords: async function () {
            //debugger
            var vueObj = this;
            var url = `ticketingId=${vueObj.ticketId}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}&lang=EN`;
            await DataServices.GetTrackHistoryList(url).then(function (response) {
                //debugger
                vueObj.Headers.forEach((column) => {
                    column.settings = null;
                    if (column.COLUMN_NAME == 'WORKORDER_NUMBER') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                    if (column.COLUMN_NAME == 'TRACK_START_TIME') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                    if (column.COLUMN_NAME == 'TRACK_END_TIME') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                    if (column.COLUMN_NAME == 'TRACK_DESCRIPTION') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                });
                if (response.data != "" && response.data != null) {
                    if (response.data.result.DATA.length > 0) {
                        vueObj.TrackHistoryListing = response.data.result.DATA;
                        vueObj.TotalRecords = response.data.result.DATA[0].TOTALRECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                        vueObj.IsLoad = true;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.TrackHistoryListing = [];
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.TotalRecords = 0
                    vueObj.TrackHistoryListing = [];
                }               
                setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
            })
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        }

    },
}
</script>