<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full d-flex">
                            <h5 class="modal-title">{{ $t('Claim') }}</h5>
                            <button type="button" class="close" v-on:click="Close">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                            <span class="user-guide">
                                <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG"
                                    role="button" aria-expanded="false" aria-controls="collapseExample"> <em
                                        class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                                <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                    <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                        <div class="con" v-html="$t('UgAddTicketClaim')"></div>
                                    </div>
                                </div>
                            </span>
                        </div>

                        <div class="modal-body">
                            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema">
                                <template v-slot:tgslot-Description="{ data }">
                                    <ckeditor :editor="editor" @ready="onReady" v-model="data.fieldInfo.value">
                                    </ckeditor>
                                    <span name="Description" class="invalid-feedback d-block">{{ data.error }}</span>
                                </template>

                            </dynamic-form>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </section>
</template>

<script>
import DataServices from "@/services/DataService";
//import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import { Field, getValidationRules } from "@/vee-validate/index"

export default {
    props: {
        id: {
            type: Number,
            required: false
        },
        modulename: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
        callbackfunction: {
            type: Function
        },
        claimId: {
            type: Number,
            required: false
        },
        viewMode: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            editor: this.CLASSIC_EDITOR,
            content: "<h1>Some initial content</h1>",
            ClaimAmount: '',
            isLoading: false,

            TicketBindList: [],
            Description: null,
            FileName: null,
            file: null,
            ClaimTicketModel: {
                ClaimNote: '',
                ClaimValue: '',
                TicketId: 0,
                ModuleName: '',
                SubModuleCode: '',
                fileData: [],
                IsDraft: '',
                ClaimId: 0
            },
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "NumericField",
                            label: this.$t('Claim Amount'),
                            name: "ClaimAmount",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 200 },
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('Claim Note'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            settings: {
                                isInSlot: true
                            },
                            visibility: true,
                            validationRules: { "required": true, max: 200 },
                        },
                    ]
                },

                {
                    group_name: "Attachment",
                    layoutType: "single",
                    Data: [
                        {
                            astype: "FileField",
                            showLabel: false,
                            name: "Attachment",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize: 5,
                                accept: [
                                    {
                                        fileType: 'png',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'jpg',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'pdf',
                                        iconClass: 'fa-file-archive-o'
                                    }
                                ],
                                onChange: this.GetBase64String,
                                showAddIcon: false,
                                onAddButtonClick: '',
                                multiple: true
                            },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-warning save",
                    value: 'Y',
                    text: "<i class='fa fa-save pr-2'></i>Save As Draft",
                    onClick: this.UploadFileWithClaim
                },
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit",
                    onClick: this.UploadFileWithClaim
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ]
        }
    },
    created: async function () {
        if (this.$parent.ticketId > 0) {
            this.FetchData();
        }

        //this.BindBillingTicketType();
    },
    methods: {
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },
        FetchData: async function () {
            var vm = this;
            vm.isLoading = true;

            var url = `ticketId=${vm.$parent.ticketId}&claimId=${vm.$parent.claimId}&isHod=`;
            await DataServices.TicketClaimList(url).then(function (response) {
                vm.isLoading = false;
                if (response.data != null && response.data != "") {
                    (vm.FormSchema)[0].Data[0].value = response.data.claim[0].claimValue;
                    (vm.FormSchema)[0].Data[1].value = response.data.claim[0].claimNotes;
                }
            });

        },
        setFile: function (e) {
            var vm = this;
            vm.file = e.target.files[0];
            vm.FileName = e.target.files[0].name;
        },
        GetBase64String: function (evt) {
            try {
                // //debugger;
                var vm = this;
                //alert(vm.apiurl);
                vm.fileData = [];

                var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                // Closure to capture the file information.
                reader.onload = (function (theFile) {
                    return function (e) {
                        var binaryData = e.target.result;
                        //Converting Binary Data to base 64
                        var base64StringEncoded = window.btoa(binaryData);
                        //showing file converted to base64

                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);

                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;
                        // //debugger;
                        // var ggg = vm.configData;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            base64String: base64StringEncoded,
                            fileSize: f.size,
                            contentType: f.type
                        };

                        vm.fileData.push(fileObj);
                    };
                })(f);
                // Read in the image file as a data URL.
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
        },

        UploadFileWithClaim: function (btnType) {
            var vm = this;
            debugger;
            if (btnType.value == "Y") {
                vm.ClaimTicketModel.IsDraft = 'Y';
            }
            vm.ClaimTicketModel.ClaimId = vm.claimId;
            vm.ClaimTicketModel.ClaimValue = vm._data.FormSchema[0].Data[0].value;
            vm.ClaimTicketModel.ClaimNote = vm._data.FormSchema[0].Data[1].value;
            vm.ClaimTicketModel.TicketId = vm.id == 0 || vm.id == null ? vm.TicketNoteId : vm.id;
            vm.ClaimTicketModel.ModuleName = vm.modulename;
            vm.ClaimTicketModel.SubModuleCode = vm.submodulecode;
            vm.ClaimTicketModel.fileData = vm.fileData;
            var formData = JSON.stringify(vm.ClaimTicketModel);
            DataServices.AddClaim(formData).then((response) => {
                try {
                    if (response.data != null && response.data != "" && response.data.status == "Success") {
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Claim")]), "success", true, vm.$t("Alert"));
                        //debugger;                                    
                        $("input[type=text]").val('');
                        vm.Close();
                    }
                    else {
                        vm.FileSrc = '';
                        $("input[type=text]").val('');
                    }
                }
                catch (exinner) {
                    return HandleErrorOccured(exinner, 'saveClaim');
                }
            });

        },
        Close: function () {
            //debugger;
            var vueObj = this;
            (vueObj.FormSchema)[0].Data[0].value = null;
            (vueObj.FormSchema)[0].Data[1].value = null;
            vueObj.FileName = null;
            vueObj.file = null;
            vueObj.FormSchema[1].Data[0].config.onChange = this.GetBase64String;
            vueObj.$parent.isShowAddClaim = false;

            if (vueObj.viewMode == "Claims") {
                document.getElementById('tab-cliam').click();

            }
        },
    }
}
</script>